/* ------------------------------------------------------------ *\
	Article
\* ------------------------------------------------------------ */

.article {
	.article__images {
		position: relative;
		display: flex;
		height: 404px;
		overflow: hidden;
	}

	.article__image {
		height: 100%;
		padding-bottom: 0;
		position: relative;

		img {
			height: 100%;
			width: 100%;
		}

		&--large {
			width: 43%;
		}

		&--size1 {
			width: 57%;
			display: flex;
			position: relative;
		}

		&--medium {
			width: 52%;
		}

		&--small {
			width: 48%;
		}

		&--overlay {
			&:after {
				content: '';
				position: absolute;
				left: 0;
				right: 0;
				width: 100%;
				height: 100%;
				background: rgba($c-base, 0.85);
			}
		}

		.btn-alt {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			z-index: 5;
		}
	}

	.article__link {
		position: absolute;
		top: 0;
		bottom: 0;
		width: 100%;
		height: 100%;
		z-index: 10;
	}

	.article__block {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 93%;
		transform: translate(-50%, -50%);
		z-index: 5;
	}

	.article__main {
		width: 100%;
		max-width: 1230px;
		margin: auto;
		display: flex;
		justify-content: space-between;
		margin-bottom: 50px;
	}

	.article__body {
		width: 64%;
		padding-top: 30px;
	}

	.article__sidebar {
		width: 30%;
	}

	.article__groups {
		.article__group:last-child {
			.panel .panel__group {
				border-bottom: none;
				margin-bottom: 0;
				padding-bottom: 0;
			}
		}
	}

	.article__group {
		&--expanded {
			height: 655px;
			overflow: hidden;
			position: relative;
			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				left: 0;
				right: 0;
				height: 34px;
				background-color: $c-white;
				filter: blur(10px);
			}

			&.active {
				max-height: none !important;
				&:after {
					opacity: 0;
					visibility: hidden;
				}
			}
		}

		&--top-border {
			margin-top: 34px;
			padding-top: 30px;
		}
	}

	.input-expand {
		display: none;
	}

	.expand-more {
		span {
			margin: 0;
			display: inline-block;
			vertical-align: top;
		}

		.text-less {
			display: none;
		}
	}

	.input-expand:checked ~ .expand-more {
		.text-less {
			display: inline-block;
		}

		.text-more {
			display: none;
		}
	}

	.input-expand:checked ~ .article__group--expanded {
		height: auto;
		overflow: visible;
	}

	.article__btn {
		display: block;
		margin-top: 15px;
	}

	.article__image + .article__image {
		margin-left: 1px;
	}
}

@media #{$large-desktop} {
	.article {
		.article__images {
			height: 380px;
		}

		.article__block {
			width: 78%;
		}
	}
}

@media #{$small-desktop} {
	.article {
		.article__main {
			padding: 0 28px;
		}

		.article__body {
			width: 66%;
		}

		.article__sidebar {
			width: 31%;
		}

		.article__block {
			width: 90%;
		}

		.article__image {
			&--large {
				width: 51%;
			}

			&--size1 {
				width: 49%;
			}

			&--medium {
				width: 43%;
			}

			&--small {
				width: 57%;
			}
		}
	}
}

@media #{$tablet-portrait} {
	.article {
		.article__main {
			flex-direction: column;
			padding: 0 0;
			margin-bottom: 0;
		}

		.article__images {
			height: 406px;
		}

		.article__image {
			img {
				height: 100%;
				width: 100%;
			}

			&--mobile {
				width: 100%;
			}

			&--size1 {
				width: 100%;
			}
		}

		.article__pagination {
			position: absolute;
			bottom: 8px;
			right: 8px;
			z-index: 10;
			background: rgba($c-black, 0.6);
			color: $c-white;
			padding: 3px 9px 4px;
			border-radius: 100px;
			font-size: 14px;
		}

		.article__body {
			padding: 12px 24px 25px;
			width: 100%;
		}

		.article__sidebar {
			border-top: 7px solid $c-light-gray-2;
			border-bottom: 7px solid $c-light-gray-2;
			width: 100%;
		}

		.article__group {
			&--expanded {
				height: 985px;
			}

			&--top-border {
				margin-top: 0;
				padding-top: 0;
			}
		}

		.article__block {
			z-index: 50;
		}

		.article__foot {
			.panel {
				.panel__head {
					padding: 0 24px;
				}

				.panel__group {
					padding: 0 12px 0;
				}
			}
		}

		.article__group + .article__group {
			margin-top: 0;
			padding-top: 0;
		}
	}
}

@media #{$mobile} {
	.article {
		.article__images {
			height: 254px;
		}

		.article__btn {
			margin-top: 30px;
			padding: 11px 20px 12px;
			border-radius: 8px;
		}

		.article__group {
			&--top-border {
				margin-top: 31px;
				border-top: 1px solid $c-light-gray-2;
			}
		}
	}
}
