/* ------------------------------------------------------------ *\
	List
\* ------------------------------------------------------------ */

[class^='list-'] {
	list-style: none outside none;
}

/* List Info */

.list-info {
	li {
		display: inline-block;
		vertical-align: middle;
		position: relative;
		font-size: 1rem;
	}

	li + li {
		padding-left: 8px;
		margin-left: 2px;

		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			bottom: 0;
			width: 4px;
			height: 4px;
			margin: auto;
			border-radius: 50%;
			background-color: $c-light-gray-1;
		}
	}
}

/* List Rooms */

.list-rooms {
	h3 {
		font-weight: 500;
	}

	li {
		display: inline-block;
		vertical-align: middle;
	}

	li + li {
		margin-left: 28px;
	}

	span + span {
		margin-left: 7px;
	}
}

.list-rooms--alt {
	li {
		text-align: center;
	}
	i {
		margin: 0 auto 14px;
		display: block;
	}

	li + li {
		margin-left: 41px;
	}
}

@media #{$tablet-portrait} {
	.list-rooms--alt {
		li {
			i {
				margin: 0 12px 0 0;
			}
			i,
			span {
				display: inline-block;
				vertical-align: middle;
			}
		}
	}
}

@media #{$mobile} {
	.list-rooms {
		h3 {
			font-size: 16px;
		}
	}
	.list-rooms--alt {
		i {
			margin: 0 7px 0 0;
		}

		li + li {
			margin-left: 15px;
		}
	}
}

/*  List  Radios */

.list-radios {
	display: flex;
	justify-content: space-between;
	li {
		margin-top: 0 !important;
	}
	li + li {
		margin-left: 8px;
	}

	@media #{$tablet-portrait} {
		justify-content: flex-start;
		li + li {
			margin-left: 8px;
		}
	}
}

/* List Radios Alt */

.list-radios--alt {
	li {
		display: block;
	}

	li + li {
		margin-left: 0;
		margin-top: 7px;
	}
}

/*  List Sort */

.list-sort {
	a {
		font-weight: 500;
		display: block;
		text-decoration: none;
		border-radius: 4px;
		padding: 13px 0;
		transition: background 0.3s, padding 0.3s;
		&:hover {
			padding: 13px 16px;
			background: rgba($c-red, 0.1);
			color: $c-red;
		}
	}

	.current {
		a {
			padding: 13px 16px;
			background: rgba($c-red, 0.1);
			color: $c-red;
		}
	}
}

/* list search */

.list-search {
	border-top: 1px solid $c-light-gray-2;
	padding: 10px 0;
	li {
		small {
			font-size: 12px;
			color: $c-light-gray-8;
		}

		a {
			display: block;
			text-decoration: none;
			padding: 10px 14px;
			transition: background 0.1s;

			&:hover {
				background: rgba($c-black, 0.07);
			}
		}
	}

	li + li {
		margin-top: 14px;
	}

	&.js-list-search {
		opacity: 0;
		visibility: hidden;
		height: 0;
		transition: opacity 0.3s, visibility 0.3s, height 0.3s;
		&.active {
			opacity: 1;
			visibility: visible;
			height: auto;
		}
	}

	@media #{$tablet-portrait} {
		background-color: $c-light-gray-5;

		li {
			p {
				font-size: 14px;
			}
		}

		li + li {
			margin-top: 10px;
		}
	}
}

.list-search--small {
	p {
		font-size: 14px;
		font-weight: 500;
	}

	small {
		font-size: 12px;
	}

	li + li {
		margin-top: 5px;
	}
}

/* List Notifications */

.list-notifications {
	li {
		max-width: 200px;
		width: 100%;
	}
	li + li {
		margin-top: 16px;
	}

	.switch {
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
}

/* List Checks */

.list-checks {
	li {
		padding-left: 32px;
		font-size: 16px;
		font-weight: 400;
		position: relative;
		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: -2px;
			width: 20px;
			height: 20px;
			background-image: url(../images/svg/ico-check.svg);
			background-size: 100% 100%;
		}
	}

	li + li {
		margin-top: 22px;
	}
}

@media #{$mobile} {
	.list-checks {
		li {
			font-size: 14px;
		}
	}
}

/* List Paging */

.list-paging {
	text-align: center;
	counter-reset: steps-counter;
	li {
		display: inline-block;
		counter-increment: steps-counter;
		position: relative;
		z-index: 5;
	}

	span {
		display: block;
		margin: auto;
		background-color: $c-light-gray-9;
		width: 48px;
		height: 48px;
		line-height: 48px;
		border-radius: 50%;
		margin-right: 30px;
		position: relative;
		z-index: 2;
		&:before {
			content: counter(steps-counter);
			font-size: 18px;
			font-weight: 500;
			color: $c-white;
		}
	}

	li + li {
		span {
			&:after {
				content: '';
				position: absolute;
				right: 100%;
				top: 0;
				bottom: 0;
				margin: auto;
				width: 71%;
				height: 4px;
				z-index: -1;
				background-color: $c-light-gray-9;
			}
		}
	}

	.active {
		span {
			background-color: $c-red;
		}
	}

	.active + .active {
		span {
			&:after {
				background-color: $c-red;
			}
		}
	}
}

@media #{$mobile} {
	.list-paging {
		li {
			span {
				width: 24px;
				height: 24px;
				line-height: 24px;
				margin-right: 21px;
				&:before {
					font-size: 14px;
				}
			}
		}

		li + li {
			span {
				&:after {
					width: 100%;
					height: 2px;
				}
			}
		}
	}
}

/* List Actions */

.list-actions {
	.link-map {
		display: block;
		width: 50px;
		height: 50px;
		line-height: 48px;
		background: rgba($c-base, 0.6);
		box-shadow: 0px 5px 10px rgba($c-black, 0.15);
		border-radius: 50%;
		text-align: center;
		transition: background 0.3s;
		&:hover {
			background: rgba($c-base, 1);
		}

		span {
			position: absolute;
			top: -3px;
			right: -4px;
			width: 20px;
			height: 20px;
			line-height: 16px;
			border-radius: 50%;
			background-color: $c-white;
			border: 1px solid $c-light-gray-3;
		}
	}
	li + li {
		margin-top: 16px;
	}
}

/* List Actions Alt */

.list-actions-alt {
	li {
		display: inline-block;
	}

	li + li {
		margin-left: 12px;
	}
}

@media #{$tablet-portrait} {
	.list-actions {
		.link-map {
			display: block;
			width: 40px;
			height: 40px;
			line-height: 36px;
			.ico-globe {
				width: 14px;
				height: 14px;
			}
		}
	}
	.list-actions-alt {
		li {
			margin-right: 12px;
		}

		li + li {
			margin-left: 0;
		}
	}
}

/* List Pins */

.list-pins {
	.pin {
		position: absolute;
	}
	.pin {
		position: absolute;
		top: 103px;
		right: 412px;
		&--plus {
			position: absolute;
			top: 290px;
			right: 536px;
		}
	}
}

@media #{$small-desktop} {
	.list-pins {
		.pin {
			&--plus {
				right: 50px;
			}
		}
	}
}

@media #{$mobile} {
	.list-pins {
		.pin {
			top: 20px;
			right: 20px;

			&--plus {
				top: 200px;
				right: 50px;
			}
		}
	}
}

/* List Product Info */

.list-product-info {
	li {
		display: inline-block;
		font-size: 16px;
	}
	li + li {
		margin-left: 16px;
	}
}

@media #{$small-desktop} {
	.list-product-info {
		li {
			font-size: 14px;
		}
	}
}

/* List images */

.list-images {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	padding: 20px 0;
	width: 100%;

	li {
		margin: 0 10px 20px;
		width: 48%;
		padding-bottom: 33%;
	}
}

@media #{$small-desktop} {
	.list-images {
		li {
			width: 47%;
		}
	}
}

@media #{$tablet-portrait} {
	.list-images {
		li {
			padding-bottom: 50%;
			margin: 0 5px 10px;
		}
	}
}

@media #{$mobile} {
	.list-images {
		padding: 0;
		li {
			width: 100%;
			padding-bottom: 62%;
			margin: 0 0 5px;
		}
	}
}

/* List Services */

.list-services {
	li {
		margin-bottom: 10px;
	}

	strong {
		font-weight: 500;
	}

	&--column {
		column-count: 2;
		column-gap: 100px;
	}

	&--small {
		li {
			margin-bottom: 6px;
		}
	}
}

@media #{$small-desktop} {
	.list-services {
		&--column {
			column-gap: 35px;
		}
	}
}

@media #{$mobile} {
	.list-services {
		&--column {
			column-count: 1;
			li {
				margin-bottom: 15px;
			}
		}
	}
}

/* List Vehicles */

.list-vehicles {
	li {
		display: inline-block;
		vertical-align: middle;
	}

	span {
		display: inline-block;
		vertical-align: middle;
		margin-left: 6px;
	}

	li + li {
		margin-left: 12px;
	}
}

/* List Default */

.list-default {
	li {
		padding-left: 17px;
		position: relative;

		&:before {
			content: '';
			position: absolute;
			left: 0;
			top: 5px;
			width: 5px;
			height: 5px;
			border-radius: 50%;
			background-color: $c-light-gray-8;
		}
	}

	li + li {
		margin-top: 13px;
	}
}

@media #{$mobile} {
	.list-default {
		li {
			font-size: 12px;
		}

		li + li {
			margin-top: 5px;
		}
	}
}

/* List Filter Actions */

.list-filter-actions {
	li {
		display: inline-block;
		vertical-align: middle;
		font-size: 14px;
	}

	li + li {
		margin-left: 10px;
	}
}
