/* ------------------------------------------------------------ *\
	Slider
\* ------------------------------------------------------------ */

.slider {
	width: 100%;
	.swiper-slide {
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
		min-height: 406px;
		position: relative;
	}

	.swiper-button-next,
	.swiper-button-prev {
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		width: 46px;
		height: 46px;
		background: rgba($c-light-gray-8, 0.5);
		z-index: 5;
		border-radius: 50%;
		cursor: pointer;
		margin-top: 10px;
		transition: background 0.1s;
		text-align: center;
		&:before {
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			top: 0;
			margin: auto;
			width: 18px;
			height: 30px;
			background-size: 100% 100%;
		}
		&:hover {
			background: rgba($c-light-gray-3, 0.5);
		}
	}
	.slider__slide--overlay {
		position: relative;
		.btn-alt {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			z-index: 1;
		}
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			z-index: 0;
			background: rgba($c-base, 0.85);
		}
	}

	.swiper-button-next {
		right: 12px;
		&:before {
			background-image: url(../images/svg/ico-arrow-right.svg);
		}
	}

	.swiper-button-prev {
		left: 12px;

		&:before {
			background-image: url(../images/svg/ico-arrow-left.svg);
		}
	}
}

@media #{$tablet-portrait} {
	.slider--property {
		.swiper-pagination {
			position: absolute;
			bottom: 8px;
			right: 8px;
			z-index: 10;
			background: rgba($c-black, 0.6);
			color: $c-white;
			padding: 3px 9px 4px;
			border-radius: 100px;
			font-size: 14px;
		}
	}
}

@media #{$mobile} {
	.slider {
		height: 100%;
		.slider__clip {
			height: 100%;
		}
		.swiper-slide {
			min-height: auto;
			height: 100%;
		}

		.swiper-button-next,
		.swiper-button-prev {
			display: none;
		}
	}

	.slider--property {
		.swiper-slide {
			min-height: 254px;
		}
	}

	.slider--blur {
		.swiper-slide {
			min-height: 300px;
		}
	}
}
