/* ------------------------------------------------------------ *\
	Steps
\* ------------------------------------------------------------ */

.steps {
	text-align: center;
	max-width: 770px;
	margin: auto;

	.list-paging {
		margin-bottom: 48px;
	}
}

@media #{$tablet-portrait} {
	.steps {
		.list-paging {
			margin-bottom: 20px;
		}
	}
}

@media #{$mobile} {
	.steps {
		.list-paging {
			margin-bottom: 0;
		}
	}
}
