/* ------------------------------------------------------------ *\
	Block
\* ------------------------------------------------------------ */

.block {
	min-width: 300px;
	// background-color: rgba($c-red, 0.05);
	border-radius: 4px;
	padding: 20px 30px;
	text-align: center;

	p {
		font-size: 20px;
		font-weight: 400;
		margin-bottom: 13px;
	}

	&--large {
		width: 78%;
		margin: auto;
		padding: 27px 24px;
		p {
			font-size: 14px;
			width: 50%;
			margin-bottom: 0;
		}

		.block__btn {
			min-width: 240px;
		}
	}

	&--flex {
		display: flex;
		justify-content: space-between;
		text-align: left;
		align-items: center;
	}
}

@media #{$tablet-portrait} {
	.block {
		&--large {
			width: 100%;
		}
	}
}

@media #{$mobile} {
	.block {
		min-width: 210px;
		&--flex {
			flex-direction: column;
			text-align: center;
		}

		&--large {
			padding: 16px 12px;
			p {
				width: 100%;
				margin-bottom: 10px;
			}

			.block__btn {
				min-width: 152px;
			}
		}
	}
}

/* ------------------------------------------------------------ *\
	Block Calculator
\* ------------------------------------------------------------ */

.block-calculator {
	padding: 25px 50px 32px 20px;
	width: 100%;
	background-color: $c-light-gray-5;
	border-radius: 8px;

	.block__row {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 7px 7px 7px 0;
		border-bottom: 1px solid $c-light-gray-2;
	}

	.block__title {
		i {
			margin-right: 17px;
		}
		p {
			display: inline-block;
			vertical-align: middle;
		}
	}

	.block__aside {
		width: 27%;
	}

	.block__controls {
		position: relative;
	}

	.block__field {
		width: 100%;
		background-color: $c-white;
		font-weight: 500;
		border: 1px solid $c-light-gray-11;
		padding: 8px 66px 7px 9px;
	}

	.block__sign {
		position: absolute;
		top: 9px;
		right: 15px;
		color: $c-light-gray-8;
		font-weight: 500;
		z-index: 1;
	}

	.block__select {
		background-color: transparent;
		border: none;
		padding: 8px 0;
		background: url(../images/svg/ico-arrow-2.svg) no-repeat right 16px
			center;
		appearance: none;
		width: 100%;
	}

	.block__foot {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-top: 20px;

		h5 {
			font-weight: 500;
		}
	}

	.block__result {
		width: 28%;
		font-size: 20px;
		font-weight: 500;
	}
}

@media #{$small-desktop} {
	.block-calculator {
		padding: 25px 12px 32px 20px;

		.block__aside {
			width: 31%;
		}

		.block__result {
			text-align: right;
			padding-right: 10px;
		}
	}
}

@media #{$mobile} {
	.block-calculator {
		padding: 10px;

		.block__row {
			padding-right: 0;
		}

		.block__title {
			.ico-info {
				margin-right: 2px;
			}
		}

		.block__aside {
			width: 44%;
		}

		.block__field {
			padding-right: 50px;
		}

		.block__result {
			width: 32%;
		}
	}
}

/* ------------------------------------------------------------ *\
	Block Account
\* ------------------------------------------------------------ */

.block-account {
	background-color: $c-white;
	border-radius: 3px;
	border-top: 3px solid $c-red;
	padding: 32px 37px 44px;

	.block__head {
		margin-bottom: 33px;
		h5 {
			margin-bottom: 4px;
			font-size: 18px;
			font-weight: 500;
		}
	}

	.block__content {
		display: flex;
		justify-content: space-between;
	}

	.block__entry {
		width: 48%;
	}

	.block__image {
		width: 27%;
		text-align: center;
	}

	.block__actions {
		margin-top: 26px;
	}

	.ico-lock-stroke {
		svg {
			stroke: $c-light-gray-8;
		}
	}
}

@media #{$large-desktop} {
	.block-account {
		padding: 20px;
	}
}

@media #{$small-desktop} {
	.block-account {
		width: 90%;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		.block__entry {
			width: 70%;
		}
	}
}

@media #{$mobile} {
	.block-account {
		position: static;
		top: 0;
		left: 0;
		transform: translate(0, 0);
		padding: 10px;
		margin: auto;

		.block__head {
			margin-bottom: 10px;
			p {
				font-size: 12px;
			}
		}

		.block__content {
			flex-wrap: wrap;
		}

		.block__entry {
			width: 100%;
		}

		.block__image {
			text-align: center;
			width: 100%;
		}

		.block__actions {
			margin-top: 10px;
		}

		.ico-lock-stroke {
			width: 23px;
			height: 31px;
		}
	}
}
