/* ------------------------------------------------------------ *\
	Form Steps
\* ------------------------------------------------------------ */

.form-steps {
	width: 100%;
	margin: auto;
	border-radius: 4px;
	background-color: $c-white;
	min-height: 428px;
	max-height: 542px;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	padding: 40px 0;

	form {
		height: 100%;
		width: 100%;
	}

	.form__step {
		display: none;
		justify-content: center;
		flex-direction: column;
		align-items: center;
		height: 100%;
		&.active {
			display: flex;
		}
	}

	.form__head {
		margin-bottom: 15px;
		max-width: 480px;
		h2 {
			font-weight: 600;
			margin-bottom: 17px;
		}

		p {
			font-size: 16px;
			font-weight: 400;
		}
	}

	.form__head--border {
		padding: 10px 0 25px;
		border-bottom: 1px solid $c-light-gray-2;
		p {
			font-size: 18px;
			margin-bottom: 15px;
		}
	}

	.form__body {
		max-width: 670px;
		width: 100%;
		&--small {
			max-width: 480px;
			width: 100%;
		}
	}

	.form__controls {
		width: 100%;
	}

	.form__field {
		text-align: center;
		width: 100%;
		border: 1px solid $c-light-gray-3;
		border-radius: 4px;
		font-size: 16px;
		font-weight: 400;
		padding: 14px;
		&::placeholder {
			color: $c-light-gray-1;
		}

		&--small {
			font-size: 14px;
		}
	}

	.form__info {
		margin-top: 17px;
	}

	.form__actions {
		margin-top: 44px;
		width: 100%;
		.btn {
			width: 100%;
		}

		&--small {
			max-width: 480px;
			margin: 44px auto 0;
		}
	}

	.form__cols {
		display: flex;
		justify-content: space-between;
	}

	.form__col {
		width: 31%;
	}

	.form__subtitle {
		border-bottom: 1px solid $c-light-gray-2;
		max-width: 480px;
		padding-bottom: 19px;
		margin-bottom: 20px;
		p {
			font-size: 16px;
		}
	}

	.form__link {
		margin-top: 38px;
		a {
			text-decoration: none;
			color: $c-red;
		}
	}

	.form__label {
		margin-bottom: 16px;
		display: block;
	}

	.form__row {
		&--small {
			max-width: 312px;
			margin: auto;

			.form__controls {
				max-width: 207px;
				margin: auto;
			}
		}
	}

	.form__row + .form__row {
		margin-top: 24px;
	}

	.number {
		font-size: 36px;
		font-weight: 600;
	}
}

@media #{$tablet-portrait} {
	.form-steps {
		padding: 20px 0;
	}
}

@media #{$mobile} {
	.form-steps {
		padding: 16px 0;
		max-height: 100%;
		align-items: flex-start;
		justify-content: flex-start;

		.form__head {
			h2 {
				font-size: 18px;
				margin-bottom: 8px;
			}

			p {
				font-size: 14px;
			}
		}

		.form__subtitle {
			padding-bottom: 11px;
			margin-bottom: 14px;
			p {
				font-size: 14px;
			}
		}

		.form__actions {
			margin-top: 24px;
		}

		.form__cols {
			flex-wrap: wrap;
		}

		.form__col {
			width: 100%;
		}

		.form__col + .form__col {
			margin-top: 12px;
		}

		.form__row {
			&--small {
				max-width: 100%;
				.form__controls {
					max-width: 100%;
				}
			}
		}

		.form__link {
			margin-top: 23px;
		}
	}
}

/* ------------------------------------------------------------ *\
	Form Access
\* ------------------------------------------------------------ */

.form-access {
	.form__head {
		margin-bottom: 32px;
	}

	.form__message {
		margin-bottom: 40px;
		padding: 6px;
		background-color: $c-dark-green;
		color: $c-white;
		text-align: center;
		border-radius: 100px;
		span {
			margin-left: 6px;
			display: inline-block;
			vertical-align: middle;
		}
	}

	.form__title {
		font-weight: 600;
	}

	.form__controls {
		display: block;
		border: 1px solid $c-light-gray-3;
		border-radius: 4px;
		display: flex;
		align-items: center;
		padding: 12px 16px;
	}

	.form__field {
		margin-left: 17px;
		border: none;
		font-size: 16px;
		background-color: transparent;
		&::placeholder {
			color: $c-dark-gray;
		}
	}

	.form__row + .form__row {
		margin-top: 16px;
	}

	.form__actions {
		margin-top: 16px;
	}

	.form__link {
		font-size: 16px;
		color: $c-red;
		text-decoration: none;
		margin-top: 25px;
		display: inline-block;
		&:hover {
			text-decoration: underline;
		}
	}

	.form__terms {
		margin-bottom: 32px;
		a {
			color: $c-red;
			text-decoration: none;
		}
	}

	.btn {
		width: 100%;
		padding: 16px;
	}

	@media #{$mobile} {
		.form__title {
			font-size: 18px;
		}

		.form__head {
			margin-bottom: 25px;
		}

		.form__actions {
			margin-top: 12px;
		}

		.form__link {
			font-size: 14px;
			margin-top: 14px;
		}

		.form__terms {
			margin-bottom: 25px;
		}

		.form__row + .form__row {
			margin-top: 11px;
		}
	}
}

/* ------------------------------------------------------------ *\
	Form Subscribe
\* ------------------------------------------------------------ */

.form-subscribe {
	background-color: $c-white;
	border: 1px solid $c-light-gray-2;
	border-top: 3px solid $c-red;
	border-radius: 0 0 4px 4px;
	box-shadow: 0px 4px 4px rgba($c-black, 0.25);
	padding: 20px 24px;

	.form__head {
		margin-bottom: 20px;

		h4 {
			font-weight: 500;
		}
		
		h5 {
			font-size: 18px;
			font-weight: 600;
		}
	}

	.form__controls {
		width: 100%;
		background-color: $c-light-gray-4;
		border-radius: 4px;
		padding: 12px 12px 10px;
		display: flex;
		align-items: center;

		i {
			margin-right: 12px;
		}
	}

	.form__actions {
		width: 100%;
		margin-top: 10px;
		.btn {
			width: 100%;
		}
	}

	.form__row + .form__row {
		margin-top: 10px;
	}
}

@media #{$tablet-portrait} {
	.form-subscribe {
		padding: 24px 24px;
		box-shadow: none;
		border-top: none;
	}
}

@media #{$mobile} {
	.form-subscribe {
		padding: 25px 24px;

		.form__head {
			margin-bottom: 25px;
		}
	}
}
