/* ------------------------------------------------------------ *\
	Section
\* ------------------------------------------------------------ */

.section {
	padding: 34px 0 47px;
	.section__title {
		font-weight: 600;
		margin-bottom: 18px;
	}

	.section__head {
		margin-bottom: 32px;
		&--center {
			text-align: center;
		}
	}

	.section__actions {
		margin-top: 38px;
	}

	.section__form {
		max-width: 435px;
		margin: auto;
	}

	.section__info {
		h5 {
			margin-bottom: 17px;
		}

		p {
			line-height: 1.79;
		}
	}

	.section__group + .section__group {
		margin-top: 54px;
	}
}

@media #{$tablet-portrait} {
	.section {
		padding: 20px 0;
		.section__head {
			margin-left: 10px;
			margin-right: 10px;
			h2 {
				font-size: 20px;
			}
		}
	}
}

@media #{$mobile} {
	.section {
		padding: 15px 0 23px;

		.section__title {
			margin-bottom: 7px;
			font-size: 22px;
		}

		.section__head {
			margin-bottom: 15px;
		}
	}
}

/* ------------------------------------------------------------ *\
	Section Products
\* ------------------------------------------------------------ */

.section--products {
	padding: 20px 40px 47px;
}

@media #{$small-desktop} {
	.section--products {
		padding: 20px 10px 47px;
	}
}

@media #{$mobile} {
	.section--products {
		margin: 0;
	}
}
