/* ------------------------------------------------------------ *\
	Footer
\* ------------------------------------------------------------ */

.footer {
	color: $c-gray;
	.footer__head {
		margin-bottom: 47px;
	}

	.footer__row {
		padding: 33px 0 30px;
		border-top: 1px solid $c-light-gray-2;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
	}

	.footer__content {
		h5 {
			font-weight: 500;
			margin-bottom: 24px;
		}

		p {
			font-weight: 400;
			line-height: 1.38;
			margin-bottom: 20px;
			font-size: 1rem;
		}

		h6 {
			font-size: 1.2rem;
			margin-bottom: 24px;
		}

		&--small {
			p {
				font-size: 14px;
				line-height: 1.79;
			}

			strong {
				font-weight: 500;
			}
		}
	}

	.footer__title {
		margin-bottom: 16px;
	}

	.footer__nav {
		flex: 0 4 25%;

		ul {
			list-style-type: none;
		}

		li + li {
			margin-top: 16px;
		}

		a {
			text-decoration: none;
			&:hover {
				color: $c-red;
			}
		}
	}
}

@media #{$tablet-portrait} {
	.footer {
		padding-bottom: 80px;

		.footer__row {
			padding: 0;
			border-top: none;

			&--no-border {
				border-top: none;
			}
		}

		.footer__content {
			padding: 0;
			margin-bottom: 0;
			width: 100%;

			p {
				font-size: 1rem;
			}
			h6 {
				margin-bottom: 0;
				font-size: 1.2rem;
			}
			.footer__title {
				border-bottom: none;
			}
		}

		.footer__head {
			margin-bottom: 30px;
		}

		.footer__nav {
			flex: 0 1 100%;

			ul {
				a {
					padding: 10px 0;
					display: block;
				}

				li + li {
					margin-top: 0;
				}
			}

			&.active {
				ul {
					opacity: 1;
				}
			}
		}

		.footer__entry {
			& > p {
				padding-top: 20px;
			}
			h6 {
				margin-bottom: 26px;
			}
		}

		.footer__title {
			position: relative;
			font-size: 18px !important;
			font-weight: 500;
			margin-bottom: 0;
			padding: 24px 0;
			border-top: 1px solid $c-light-gray-2 !important;
			.ico-arrow-dropdown {
				position: absolute;
				right: 8px;
				top: 31px;
				transition: transform 0.3s;
				svg {
					display: block;
				}
			}

			&.active {
				.ico-arrow-dropdown {
					transform: rotate(180deg);
				}
			}
		}

		.footer__aside,
		.footer__inner {
			width: 100%;
		}

		&--property {
			.shell {
				padding-left: 24px;
				padding-right: 24px;
			}
		}
	}
}

@media #{$mobile} {
	.footer {
		.footer__content {
			padding: 0;
			h5 {
				margin-bottom: 0;
			}
			p {
				margin-bottom: 26px;
			}
		}

		.footer__entry {
			& > p {
				padding-top: 10px;
				margin-bottom: 16px;
			}
		}
	}
}
