/* ------------------------------------------------------------ *\
	Table
\* ------------------------------------------------------------ */

.table {
	thead {
		th {
			text-align: right;
			font-weight: 500;
			padding: 13px 13px 14px;
			border-bottom: 1px solid $c-light-gray-2;
			&:first-child {
				padding: 13px 0 14px;
				text-align: left;
			}
		}
	}

	tbody {
		td {
			text-align: right;
			font-weight: 500;
			padding: 13px 13px 14px;
			border-bottom: 1px solid $c-light-gray-2;
			&:first-child {
				text-align: left;
				padding: 13px 0 14px;
			}
		}

		tr {
			&:last-child {
				td {
					border: none;
				}
			}
		}
	}
}

@media #{$mobile} {
	.table {
		overflow-x: auto;
		table {
			font-size: .85rem;
			width: 100%;
		}
	}

	.popup--table {
		.table {
			tbody {
				td {
					padding: 20px 2px;
				}
			}
		}
	}
}

/* ------------------------------------------------------------ *\
	Table Alt
\* ------------------------------------------------------------ */

.table-alt {
	width: 100%;
	border: 1px solid $c-light-gray-2;
	border-radius: 4px;
	overflow: hidden;
	thead {
		th {
			text-align: left;
			background-color: $c-light-gray-4;
			text-transform: uppercase;
			color: $c-black;
			padding: 14px 0;
			font-weight: 500;

			&:first-child {
				padding-left: 26px;
			}
		}
	}

	tbody {
		td {
			padding: 26px 0 0;
			font-size: 16px;
			width: 145px;
			small {
				font-size: 12px;
				display: block;
				margin-top: 3px;
				color: $c-gray;
			}

			&:first-child {
				padding-left: 26px;
			}
			&:last-child {
				width: 428px;
			}
		}

		tr {
			&:last-child {
				td {
					padding-bottom: 26px;
				}
			}
		}
	}
}

/* Table Alt Price */

.table-alt--price {
	thead {
		th {
			color: $c-base;
			background-color: $c-light-gray-12;

			&:first-child {
				padding-left: 40px;
			}
		}
	}

	tbody {
		&.table-group {
			border-top: 1px solid $c-light-gray-2;
			border-bottom: 1px solid $c-light-gray-2;
		}

		td, th {
			padding: .75rem!important;
		}

		.table-timeline {
			text-align: center;
			vertical-align: middle;
			width: 30px!important;
			position: relative;

			.timeline-bullet {
				width: 10px;
				height: 10px;
				border-radius: 50%;
				background-color: grey;
				display: block;
				outline: 3px solid #fff;
			}
		
			.timeline-line {
				height: 100%
			}

			.fill-green {
				background-color: $c-green!important;
			}
			
			.fill-red {
				background-color: $c-dark-red!important;
			}
		}
		
		.has-both:before,.has-bottom:before,.has-top:after {
			content: "";
			display: inline-block;
			height: 50%;
			width: 4px;
			background-color: grey;
			position: absolute;
			left: calc(50% - 2px);
			z-index: -1
		}
		
		.has-bottom:before {
			bottom: -1px;
		}
		
		.has-top:after {
			top: -1px;
		}
		
		.has-both:before {
			height: 100%;
			height: calc(100% + 2px);
			top: -1px;
			bottom: -1px
		}
		
		.has-bottom div,.has-top div {
			display: inline-block;
		}
		
		td {
			width: auto;
			padding: 24px 0 20px;
			&:first-child {
				padding-left: 40px;
			}
			&:last-child {
				width: auto;
			}
		}
	}
}

@media #{$small-desktop} {
	.table-alt--price {
		thead {
			th {
				&:first-child {
					padding-left: 23px;
				}
			}
		}

		tbody {
			td {
				&:first-child {
					padding-left: 23px;
				}
			}
		}
	}
}

@media #{$tablet-portrait} {
	.table-alt {
		thead {
			th {
				&:first-child {
					padding-left: 20px;
				}
			}
		}
		tbody {
			td {
				&:first-child {
					padding-left: 20px;
				}
			}
		}
	}
}

@media #{$mobile} {
	.table-alt--price {
		border: none;

		tbody {
			td {
				font-size: 14px;
				padding: 17px 0 13px;
				&:first-child {
					padding-left: 11px;
				}
			}

			tr {
				&:last-child {
					td {
						padding-bottom: 13px;
					}
				}
			}
		}

		strong {
			margin-bottom: 2px;
			font-weight: 500;
			display: block;
		}
	}
}

/* ------------------------------------------------------------ *\
	Table Stats
\* ------------------------------------------------------------ */

.table-stats {
	width: 100%;
	thead {
		th {
			text-align: left;
			padding: 15px 0 13px;
			font-weight: 500;
			border-bottom: 1px solid $c-light-gray-2;
		}
	}

	tbody {
		td {
			padding: 14px 0 7px;
			border-bottom: 1px solid $c-light-gray-2;
		}
	}

	span {
		display: inline-block;
		vertical-align: middle;
		color: $c-light-gray-8;
	}

	.ico-arrow-1 {
		margin: 0 7px;
		display: inline-block;
		vertical-align: middle;
		width: 4px;
		height: 7px;
		svg {
			stroke: $c-gray;
		}
	}

	.table__info {
		font-size: 12px;
		margin-top: 16px;
	}
}

/* Table Stats Alt */

.table-stats--alt {
	tbody {
		td {
			padding: 14px 0 13px;

			&:last-child {
				text-align: right;
			}
		}
	}
}

.table-stats--alt + .table-stats--alt {
	margin-top: 10px;
}

@media #{$mobile} {
	.table-stats {
		tbody {
			td {
				padding: 14px 0 12px;
			}
		}
	}
}

/* ------------------------------------------------------------ *\
	Table stats alt
\* ------------------------------------------------------------ */

.table-stats-alt {
	.table__head {
		padding: 15px 0 13px;
		font-weight: 500;
		border-bottom: 1px solid $c-light-gray-2;
	}

	.table__row {
		display: flex;
		justify-content: space-between;
		padding: 14px 0;
		border-bottom: 1px solid $c-light-gray-2;

		&:hover {
			color: $c-red;
		}
	}

	.table__col {
		width: 25%;
		text-align: left;
		white-space: nowrap;
		&:last-child {
			text-align: right;
		}
	}
}

.table-stats-alt + .table-stats-alt {
	margin-top: 10px;
}
