/* ------------------------------------------------------------ *\
	Logo
\* ------------------------------------------------------------ */

.logo {
	display: inline-block;
	vertical-align: middle;
	width: 133px;
	height: 37px;

	svg {
		width: 100%;
		height: 100%;
	}
}

.logo-small {
	display: inline-block;
	margin-right: 1rem;
	svg {
		vertical-align: middle;
		width: 30px;
		height: 30px;
	}
}

