/* ------------------------------------------------------------ *\
	Statistics
\* ------------------------------------------------------------ */

.statistics {
	i {
		vertical-align: middle;
	}
	span {
		display: inline-block;
		vertical-align: middle;
	}
	strong {
		font-weight: 500;
	}
}

.statistics--large {
	font-size: 28px;
	span {
		margin-left: 10px;
		font-weight: 600;
		font-size: 28px;
		small {
			font-size: 20px;
		}
	}
}
