/* ------------------------------------------------------------ *\
	Button
\* ------------------------------------------------------------ */

.btn {
	display: inline-block;
	vertical-align: middle;
	text-align: center;
	cursor: pointer;
	text-decoration: none;
	appearance: none;
	position: relative;
	z-index: 2;
	line-height: 1;
	font-weight: 500;
	padding: 12px 17px;
	font-size: 16px;
	border-radius: 4px;
	transition: background 0.3s, color 0.3s;
	border: none;

	&--block {
		display: block;
		padding-left: 0;
		padding-right: 0;
	}

	&--red {
		background-color: $c-red;
		color: $c-white;

		&:hover {
			background-color: $c-dark-red;
		}
	}

	&--orange {
		background-color: $c-orange-2;
		color: $c-white;

		&:before {
			background-color: $c-white;
		}

		&:hover {
			color: $c-orange;
		}
	}

	&--small {
		font-size: 14px;
		padding: 9px 14px;
		font-weight: 400;
	}

	&--medium {
		padding: 12px 14px;
		font-size: 14px;
		font-weight: 400;
	}

	&--icon {
		min-width: 136px;
		span {
			margin-left: 4px;
			display: inline-block;
			vertical-align: middle;
		}
	}
}

@media (max-width: 1040px) {
	.btn--small {
		padding: 9px 8px;
	}
}

/* Button Access */

.btn-access {
	display: inline-block;
	vertical-align: middle;
	text-align: center;
	cursor: pointer;
	text-decoration: none;
	appearance: none;
	position: relative;
	z-index: 2;
	font-size: 14px;
	line-height: 1;
	font-weight: 500;
	color: $c-red;
	background-color: $c-white;
	padding: 9px 17px;
	border-radius: 4px;
	border: 1px solid $c-light-gray-7;
	transition: background-color 0.3s;

	&:hover {
		background-color: $c-light-gray-4;
	}

	@media #{$tablet-portrait} {
		font-size: 16px;
		text-transform: uppercase;
		padding: 14px 17px;
	}
}

/* Button Sort */

.btn-sort {
	display: inline-block;
	vertical-align: middle;
	text-align: center;
	cursor: pointer;
	text-decoration: none;
	appearance: none;
	position: relative;
	left:15px;
	padding: 8px 16px;
	font-size: 14px;
	border: 1px solid $c-light-gray-3;
	border-radius: 4px;
	background-color: $c-white;
	transition: background-color 0.3s;

	strong {
		font-weight: 500;
	}

	.ico-sort {
		display: inline-block;
		vertical-align: middle;
		width: 16px;
		height: 15px;
		margin-right: 7px;
	}

	&:hover {
		background-color: $c-light-gray-6;
	}
}

/* Button Alt */

.btn-alt {
	font-weight: 400;
	font-size: 14px;
	display: inline-block;
	vertical-align: middle;
	text-align: center;
	cursor: pointer;
	text-decoration: none;
	appearance: none;
	line-height: 1;
	padding: 5px 10px;
	border-radius: 4px;
	transition: background 0.3s, color 0.3s;
	border: none;
	box-shadow: 0px 1px 2px rgba($c-black, 0.10);
	&--white {
		background-color: $c-white;
	}

	&--icon {
		i {
			margin-right: 4px;
		}

		.ico-map,
		.ico-share {
			svg {
				stroke: $c-base;
				path {
					stroke: $c-base;
				}
			}
		}
	}

	&--gray {
		background-color: $c-light-gray-2;
		box-shadow: none;
	}

	&:hover {
		background-color: $c-red;
		color: $c-white;

		.ico-map,
		.ico-share {
			svg {
				stroke: $c-white;
				path {
					stroke: $c-white;
				}
			}
		}
	}
}

/* Button Alt Large */

.btn-alt-large {
	font-weight: 400;
	font-size: 16px;
	display: inline-block;
	vertical-align: middle;
	text-align: center;
	cursor: pointer;
	text-decoration: none;
	appearance: none;
	line-height: 1;
	padding: 12px 12px;
	border-radius: 4px;
	transition: background 0.3s, color 0.3s;
	border: none;
	box-shadow: 0px 1px 2px rgba($c-black, 0.10);
	&--white {
		background-color: $c-white;
	}

	&--icon {
		i {
			margin-right: 4px;
		}

		.ico-map,
		.ico-share {
			svg {
				stroke: $c-base;
				path {
					stroke: $c-base;
				}
			}
		}
	}

	&--gray {
		background-color: $c-light-gray-2;
		box-shadow: none;
	}

	&:hover {
		background-color: $c-red;
		color: $c-white;

		.ico-map,
		.ico-share {
			svg {
				stroke: $c-white;
				path {
					stroke: $c-white;
				}
			}
		}
	}
}

/* Button Border */

.btn-border {
	display: inline-block;
	vertical-align: middle;
	text-align: center;
	cursor: pointer;
	text-decoration: none;
	appearance: none;
	line-height: 1;
	border: 1px solid $c-light-gray-3;
	background-color: $c-white;
	font-size: 14px;
	min-width: 100px;
	padding: 6px 20px 7px;
	border-radius: 4px;
	transition: background 0.3s, color 0.3s;
	span {
		margin-left: 10px;
	}
	span,
	i {
		display: inline-block;
		vertical-align: middle;
	}

	&--red {
		border-color: $c-red;
		color: $c-red;
		padding: 14px 20px 14px;
		font-size: 16px;
	}

	&--small {
		padding: 4px 10px 5px;
	}

	&--block {
		display: block;
		width: 100%;
	}

	&--alt {
		font-weight: 400;
		border-radius: 8px;
		padding: 10px 20px 10px;
	}

	&--filter {
		padding: 8px 16px;
		.ico-pin-large {
			svg {
				fill: $c-gray;
			}
		}

		.ico-list {
			width: 16px;
			height: 16px;
		}
	}

	&:hover {
		background-color: $c-light-gray-6;
	}
}

/* Button Map */

.btn-map {
	position: relative;
	display: block;
	background-color: $c-white;
	width: 50px;
	height: 50px;
	border-radius: 50%;
	border: 1px solid $c-light-gray-10;
	box-shadow: 0px 3px 5px rgba($c-black, 0.15);
	transition: background-color 0.3s;
	margin: 10px 0;
	
	span {
		position: absolute;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		margin: auto;
		width: 14px;
		height: 2px;
		background-color: $c-base;
	}

	span + span {
		transform: rotate(90deg);
	}

	&:hover {
		background-color: $c-light-gray-10;
	}
}


@media #{$tablet-portrait} {
	.btn-map {
		width: 40px!important;
		height: 40px!important;
	}

	.btn--menu {
		padding: 0 1rem;
		margin-left: -1rem;
	}
}