/* ------------------------------------------------------------ *\
	Nav
\* ------------------------------------------------------------ */

.nav {
	& > ul {
		& > li {
			display: inline-block;
			position: relative;
			color: $c-dark-gray;
			& > a {
				font-size: 16px;
				font-weight: 500;
				line-height: 1.25;
				text-decoration: none;
				padding: 22px 0;
			}

			&:hover {
				& > a {
					color: $c-black;
					.ico-heart-outlined {
						svg {
							fill: $c-black;
							stroke: $c-black;
						}
					}
				}
			}

			.ico-heart-solid {
				width: 23px;
				height: 23px;
				&:hover {
					svg {
						fill: $c-black;
					}
				}
			}

			.ico-bell-solid {
				width: 23px;
				height: 23px;
				&:hover {
					svg {
						fill: $c-black;
					}
				}
			}

			.ico-bell-solid-active {
				width: 23px;
				height: 23px;
				&:hover {
					svg {
						fill: $c-black;
					}
				}
			}

			.ico-settings-gear-solid {
				width: 23px;
				height: 23px;
				&:hover {
					svg {
						fill: $c-black;
					}
				}
			}
		}

		& > li + li {
			margin-left: 20px;
		}
	}

	.nav__dropdown {
		position: absolute;
		left: -95px;
		top: 100%;
		z-index: 70;
		margin-top: 22px;
		font-size: 14px;
		background-color: $c-white;
		color: $c-black;
		box-shadow: 0px 10px 12px rgba(0, 0, 0, 0.06);
		border: 1px solid $c-light-gray-2;
		border-radius: 4px;
		min-width: 240px;

		strong {
			display: block;
			font-weight: 500;
			padding: 12px 16px 11px;
			text-transform: uppercase;
			border-bottom: 1px solid $c-light-gray-2;
		}

		& > ul {
			padding: 8px;
			& > li {
				& > a {
					text-decoration: none;
					display: flex;
					align-items: center;
					justify-content: space-between;
					padding: 12px 8px;
					font-weight: 500;
					border-radius: 4px;
					transition: background 0.3s, color 0.3s;

					&:hover {
						background: rgba($c-red, 0.1);
						color: $c-red;
					}
				}
			}
		}

		&--small {
			min-width: 198px;
		}

		&.pull-right {
			left: auto;
			right: 0;
		}
	}

	.nav__saved {
		border-bottom: 8px solid $c-light-gray-2;
		& > ul {
			& > li {
				& > a {
					margin: 0 12px;
					display: flex;
					align-items: center;
					justify-content: space-between;
					padding: 14px 0 13px;
					font-size: 14px;
					border-top: 1px solid $c-light-gray-2;
				}
			}
		}
	}

	@media (min-width: 1024px) {
		.nav__dropdown {
			opacity: 0;
			visibility: hidden;
			transform: scale(0.9);
			transition: opacity 0.3s, visibility 0.3s, transform 0.3s;
		}
		.has-dropdown {
			&:hover {
				.nav__dropdown {
					opacity: 1;
					visibility: visible;
					transform: scale(1);
				}
			}
		}
	}
}

@media #{$small-desktop} {
	.nav {
		& > ul {
			& > li + li {
				margin-left: 30px;
			}
		}
	}
}

@media #{$tablet-portrait} {
	.nav {
		& > ul {
			padding: 0;
			& > li {
				display: block;
				& > a {
					margin: 0 12px;
					display: flex;
					justify-content: space-between;
					padding: 17px 0 14px;
					border-top: 1px solid $c-light-gray-2;
					background-color: $c-white;

					.ico-arrow-dropdown {
						position: absolute;
						right: 18px;
						top: 21px;
						transition: transform 0.3s;
						svg {
							display: block;
						}
					}
				}
			}

			// & > li:last-child {
			// 	& > a {
			// 		border-bottom: 1px solid $c-light-gray-2;
			// 	}
			// }

			& > li + li {
				margin-left: 0;
			}
		}

		.list-search {
			padding: 0;
		}
		
		.nav__dropdown {
			position: relative;
			font-size: 0.85rem;
			margin-top: 0;
			transform: scale(1);
			width: 100%;
			margin-left: 0;
			margin-right: 0;
			left: 0;
			right: 0;
			background-color: $c-light-gray-5;
			opacity: 1;
			border-radius: 0;
			border: none;

			box-shadow: none;
			pointer-events: none;
			strong {
				text-transform: capitalize;
				border: none;
			}

			ul {
				padding: 0 8px;
			}
			a {
				padding: 12px 8px;
			}

			&.pull-right {
				left: 0;
				right: 0;
			}

			.list-search {
				height: 100%;
				opacity: 1;
				visibility: visible;
			}

			&--small {
				min-width: 100%;
				width: 100%;
				left: 0;
			}
		}

		.nav__saved {
			a {
				position: relative;
			}
			.ico-arrow-dropdown {
				position: absolute;
				top: 21px;
				right: 6px;
			}

			.js-show-search {
				&.active {
					.ico-arrow-dropdown {
						transform: rotate(180deg);
					}
				}
			}
		}

		.has-dropdown {
			&.active {
				.ico-arrow-dropdown {
					transform: rotate(180deg);
				}
				.nav__dropdown {
					opacity: 1;
					pointer-events: all;
				}
			}
			&.has-border-top {
				border-top: 8px solid $c-light-gray-2;
			}
		}
	}
}

/* Nav Trigger */

.nav-trigger {
	margin-right: 15px;
	margin-left: 3px;
	position: relative;
	display: inline-block;
	width: 20px;
	height: 14px;
	span {
		position: absolute;
		display: block;
		height: 2px;
		width: 100%;
		border-radius: 2px;
		background: $c-black;
		transition: top 0.2s 0.25s, opacity 0.2s 0.25s, transform 0.2s 0s;
		&:nth-child(1) {
			top: 0;
		}
		&:nth-child(2) {
			top: 6px;
		}
		&:nth-child(3) {
			top: 12px;
		}
	}

	&--active {
		span {
			transition: background 0.2s, top 0.2s, opacity 0.2s,
				transform 0.2s 0.25s;

			&:nth-child(1),
			&:nth-child(3) {
				top: 6px;
			}

			&:nth-child(2) {
				opacity: 0;
			}
			&:nth-child(1) {
				transform: rotate(45deg);
			}

			&:nth-child(3) {
				transform: rotate(-45deg);
			}
		}
	}
}

