/* Nav Secondary */

.nav-secondary {
	margin-bottom: 20px;
	li {
		display: inline-block;
		vertical-align: middle;
	}
	a {
		font-weight: 500;
		text-decoration: none;
		&:hover {
			color: $c-red;
		}
	}
	li + li {
		margin-left: 20px;
	}
}

@media #{$tablet-portrait} {
	.nav-secondary {
		li {
			display: block;
			a {
				display: block;
				padding: 10px 0;
			}
		}

		li + li {
			margin-left: 0;
		}

		&.active {
			ul {
				opacity: 1;
			}
		}
	}
}
