/* ------------------------------------------------------------ *\
	Products
\* ------------------------------------------------------------ */

.products {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}

@media #{$tablet-portrait} {
	.products {
		&--property {
			overflow-y: scroll;
			-webkit-overflow-scrolling: touch;
			flex-wrap: nowrap;
		}
	}
}

.product {
	flex: 0 1 24%;
	position: relative;
	z-index: 5;
	margin-bottom: 20px;
	box-shadow: 0 2px 8px 0 rgba(0,0,0,0.1);

	& > a {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		display: block;
		z-index: 4;
	}

	.product__image {
		padding-bottom: 68%;
		border-radius: 4px;
		margin-bottom: 10px;
		position: relative;
		img {
			position: absolute;
			top: 0;
			left: 0;
		}
	}

	.product__head {
		margin-bottom: 5px;

		span {
			display: inline-block;
			vertical-align: middle;
		}
	}

	.product__price {
		color: $c-gray;
		font-weight: 600;
		font-size: 1.375rem!important;
    	line-height: 1.3!important;
	}

	.product__time {
		color: $c-gray;
		font-size: 14px;
		font-weight: 500;

		.ico-clock {
			position: relative;
			width: 12px;
			height: 12px;
			top: -1px;
		}
	}

	.product__feature {
		color: $c-orange-2;
		font-size: 14px;
		font-weight: 500;
	}

	.product__reduced {
		color: $c-blue;
		font-size: 14px;
		font-weight: 500;
	}

	.product__tag {
		position: absolute;
		font-size: 14px;
		top: 11px;
		left: 12px;
		padding: 5px 10px;
		background-color: $c-yellow;
		color: $c-white;
		font-weight: 500;
		border-radius: 4px;
		color: $c-white;
		background-color: $c-green;

		&--blue {
			background-color: $c-blue;
		}

		&--red {
			background-color: $c-red;
		}
	}

	.product__title {
		font-weight: 400;
		margin-bottom: 3px;

		&--nowrap {
			width: 98%;
			overflow: hidden;
			display: inline-block;
			text-overflow: ellipsis;
			white-space: nowrap;
		}
	}

	.product__subtitle {
		margin-bottom: 10px;
	}

	.product__like {
		position: absolute;
		top: 10px;
		right: 11px;
		z-index: 5;

		&:hover {
			svg {
				fill: $c-red;
				fill-opacity: 1;
			}
		}
	}

	ul + ul {
		margin-top: .5rem!important;
	}

	.product__content {
		margin: 1rem;
	}
}

@media #{$large-desktop} {
	.product {
		flex: 0 1 32%;
	}
}

/* ------------------------------------------------------------ *\
	Product Building
\* ------------------------------------------------------------ */

.product--building {
	.product__title {
		font-size: 18px;
		font-weight: 500;
	}

	.product__content {
		p {
			margin-bottom: 7px;
		}
	}
}

@media #{$tablet-portrait} {
	.product {
		flex: 0 1 48%;
	}
}

@media #{$mobile} {
	.product {
		flex: 0 1 100%;
		margin-bottom: 23px;

		.product__image {
			margin-bottom: 10px;
		}

		.product__feature {
			top: 17px;
			left: 16px;
		}

		.product__tag {
			font-size: 10px;
		}
	}
}

/* ------------------------------------------------------------ *\
	Product Large
\* ------------------------------------------------------------ */

.product--large {
	flex: 0 0 49%;
}

@media #{$small-desktop} {
	.product--large {
		flex: 0 0 100%;
	}
}

/* ------------------------------------------------------------ *\
	Prodduct Alt
\* ------------------------------------------------------------ */

.product-alt {
	.product__inner {
		padding: 25px 17px 21px;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.product__head {
		display: flex;
		align-items: center;
	}

	.product__price {
		color: $c-black;
		font-size: 18px;
		font-weight: 600;
	}

	.product__entry {
		padding-top: 9px;
		font-size: 16px;
	}

	.list-info {
		margin-left: 9px;
	}
}

@media #{$tablet-portrait} {
	.product-alt {
		.product__inner {
			padding: 12px 9px 12px;
		}
	}
}

@media #{$mobile} {
	.product-alt {
		display: flex;
		flex-direction: column;
		height: 100%;

		.product__images {
			height: 250px;
		}

		.product__inner {
			height: 22%;
		}

		.product__entry {
			padding-top: 3px;
			p {
				font-size: 14px;
				width: 260px;
        		overflow:hidden;
				white-space:nowrap;
		        text-overflow: ellipsis;
			}
		}
	}
}

/* ------------------------------------------------------------ *\
	Product Property
\* ------------------------------------------------------------ */

.product--property {
	width: 31.7%;
	margin-bottom: 29px;
	flex: 0 0 auto;
}

@media #{$small-desktop} {
	.product--property {
		width: 48%;
		flex: 0 0 auto;
	}
}

@media #{$tablet-portrait} {
	.product--property {
		margin-right: 10px;
	}
}

@media #{$mobile} {
	.product--property {
		width: 94%;

		.product__tag {
			font-size: 12px;
		}
	}
}

/* ------------------------------------------------------------ *\
	Product Blur
\* ------------------------------------------------------------ */

.product--blur {
	.product__image {
		filter: blur(0.5rem);
	}

	.product__lock {
		position: absolute;
		top: 28px;
		left: 50%;
		transform: translateX(-50%);
		z-index: 10;
	}

	.ico-lock-stroke {
		svg {
			stroke: $c-white;
		}
	}

	.product__info {
		span {
			font-size: 18px;
			font-weight: 500;
			color: $c-red;
		}
	}
}
