/* ------------------------------------------------------------ *\
	Stamp
\* ------------------------------------------------------------ */

.stamp {
	box-shadow: 0px 5px 5px rgba($c-black, 0.05);
	border: 1px solid $c-light-gray-2;
	border-radius: 4px;
	padding: 24px;
	position: relative;
	&:before {
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		width: 3px;
		height: 77px;
		margin: auto;
		background-color: $c-red;
	}

	.stamp__content {
		h6 {
			font-size: 16px;
			font-weight: 500;
			margin-bottom: 15px;
		}
	}
}
