/* ------------------------------------------------------------ *\
	Box
\* ------------------------------------------------------------ */

.box {
	.box__head {
		color: $c-white;
		padding: 62px 10px;
		text-align: center;
		background-color: $c-red;

		h2 {
			// font-weight: 600;
			margin-bottom: 10px;
		}
	}

	.box__price {
		font-size: 28px;
		font-weight: 700;
		small {
			font-weight: 500;
			font-size: 20px;
		}
	}

	.box__body {
		padding: 0 20px;
	}
}

@media #{$mobile} {
	.box {
		.box__head {
			padding: 20px 10px;

			h2 {
				font-size: 18px;
			}
		}

		.box__price {
			font-size: 16px;
			small {
				font-size: 16px;
			}
		}


		.box__body {
			padding: 0 10px;
		}
	}
}
