/* ------------------------------------------------------------ *\
	Stats
\* ------------------------------------------------------------ */

.stats {
	.stats__actions {
		margin-top: 24px;

		a + a {
			margin-left: 19px;
		}
	}
}

@media #{$mobile} {
	.stats {
		.stats__actions {
			a {
				display: block;
			}

			a + a {
				margin-left: 0;
				margin-top: 14px;
			}
		}
	}
}
