/* ------------------------------------------------------------ *\
	Form Elements
\* ------------------------------------------------------------ */

input::placeholder {
	color: $c-dark-gray;
	opacity: 1;
}

textarea::placeholder {
	color: $c-dark-gray;
	opacity: 1;
}

input:-webkit-autofill {
	-webkit-text-fill-color: inherit;
	-webkit-box-shadow: 0 0 0 1000px #fff inset;
}

.field {
	font-size: 16px;
	border: none;
	background-color: transparent;
	resize: none;
	line-height: 1.43;
	color: $c-base;
}

/* Textarea */

.textarea {
	resize: none;
	border: 1px solid $c-light-gray-3;
	padding: 12px;
	border-radius: 4px;
	max-height: 80px;
}

/* Select */

.select {
	display: block;
	width: 100%;
	border-radius: 4px;
	border: 1px solid $c-light-gray-3;
	padding: 11px 8px 9px;
	background: url(../images/svg/ico-arrow-4.svg) no-repeat right 16px center;
	appearance: none;

	&::-ms-expand {
		display: none;
	}
}

/* Radio */

.radio {
	input {
		position: absolute;
		opacity: 0;
		height: 1px;
		width: 1px;
		cursor: pointer;
		&:checked {
			& ~ label {
				background-color: rgba($c-red, 0.1);
				border-color: rgba($c-red, 0.1);
				color: $c-red;
			}
		}
	}

	label {
		display: block;
		padding: 12px;
		font-size: 16px;
		border: 1px solid $c-light-gray-3;
		border-radius: 4px;
		font-weight: 400;
	}
}

/* Radio Red */

.radio--red {
	input {
		&:checked {
			& ~ label {
				background-color: $c-red;
				border-color: $c-light-gray-3;
				color: $c-white;
			}
		}
	}
}

/* Radio Dots */

.radio--dot {
	input {
		&:checked {
			& ~ label {
				background-color: $c-white;
				color: $c-base;
				border: 1px solid $c-light-gray-3;

				&:before {
					background-color: $c-red;
				}
			}
		}
	}

	label {
		position: relative;
		padding: 12px 14px;
		&:before {
			content: '';
			position: absolute;
			top: -5px;
			left: 50%;
			transform: translateX(-50%);
			background-color: $c-white;
			border: 1px solid $c-light-gray-3;
			display: block;
			width: 11px;
			height: 11px;
			border-radius: 50%;
		}
	}
}

/* Radio Check */

.radio-check {
	position: relative;
	input {
		position: absolute;
		opacity: 0;
		height: 100%;
		width: 100%;
		cursor: pointer;
		z-index: 5;
	}

	label {
		padding: 7px 28px;
		position: relative;
		display: block;
		&:before,
		&:after {
			content: '';
			position: absolute;
		}

		&:before {
			left: 0;
			top: 5px;
			width: 20px;
			height: 20px;
			border-radius: 50%;
			border: 1px solid $c-light-gray-9;
			background-color: transparent;
			transition: background 0.3s;
		}

		&:after {
			top: 11px;
			left: 5px;
			width: 11px;
			height: 7px;
			background-image: url(../images/svg/ico-check-white.svg);
			background-size: 100% 100%;
			opacity: 0;
			visibility: hidden;
			transition: opacity 0.3s, visibility 0.3s;
		}
	}

	input:checked {
		& ~ label {
			&:before {
				background-color: $c-red;
				border-color: $c-red;
			}

			&:after {
				opacity: 1;
				visibility: visible;
			}
		}
	}
}

/* Checkbox */

.checkbox {
	display: block;
	min-height: 24px;
	input {
		position: absolute;
		opacity: 0;
		height: 1px;
		width: 1px;
		cursor: pointer;
	}

	label {
		padding-right: 33px;
		position: relative;
		display: inline-block;
		&:after,
		&:before {
			content: '';
			position: absolute;
		}

		&:before {
			top: -5px;
			right: 0;
			width: 24px;
			height: 24px;
			background-color: transparent;
			border: 1px solid $c-light-gray-3;
			border-radius: 4px;
			transition: background-color 0.3s;
		}

		&:after {
			top: 4px;
			right: 7px;
			width: 10px;
			height: 7px;
			background-image: url(../images/svg/ico-check-white.svg);
			background-size: 100% 100%;
			opacity: 0;
			visibility: hidden;
		}
	}

	input:checked {
		& ~ label {
			&:after {
				opacity: 1;
				visibility: visible;
			}
			&:before {
				background-color: $c-red;
				border-color: $c-red;
			}
		}
	}

	@media #{$tablet-portrait} {
		padding: 5px 0;
	}
}

.acc-input,
.acc-input-desktop {
	display: none;
}

.acc-label-desktop {
	display: block;
	cursor: pointer;
}

.acc-content-desktop {
	height: 0;
	overflow: hidden;
}

.acc-input-desktop:checked ~ .acc-content-desktop {
	height: auto;
	overflow: visible;
}

@media #{$tablet-portrait} {
	.acc-label {
		display: block;
		cursor: pointer;
	}

	.acc-content {
		height: 0;
		overflow: hidden;
	}

	.acc-input:checked ~ .acc-label {
		.ico-arrow-dropdown,
		.ico-arrow-2 {
			transform: rotate(180deg);
		}
	}

	.acc-input:checked ~ .acc-content {
		height: auto;
		overflow: visible;
	}
}
