/* ------------------------------------------------------------ *\
	Breadcrumbs
\* ------------------------------------------------------------ */

.breadcrumbs {
	li {
		display: inline-block;
		vertical-align: middle;
	}

	li + li {
		padding-left: 15px;
		margin-left: 15px;
		border-left: 1px solid $c-light-gray-3;
	}
	a {
		text-decoration: none;
	}

	.current {
		color: $c-red;
	}
}

@media #{$large-desktop} {
	.breadcrumbs {
		li + li {
			margin-left: 4px;
			padding-left: 9px;
		}
	}
}

@media #{$small-desktop} {
	.breadcrumbs {
		li {
			font-size: 14px;
		}
	}
}

@media #{$mobile} {
	.breadcrumbs {
		li {
			font-size: 14px;
		}

		li + li {
			margin-left: 4px;
			padding-left: 9px;
		}
	}
}

/* ------------------------------------------------------------ *\
	Breadcrumbs Alt
\* ------------------------------------------------------------ */

.breadcrumbs-alt {
	padding: 17px 40px;
	font-size: 14px;

	.shell {
		padding: 0;
		display: flex;
		align-items: center;

		.back-btn {
			margin-right: 20px;
		}
	}

	ul {
		list-style-type: none;
		li {
			display: inline-block;
			position: relative;
		}

		a {
			text-decoration: none;
			color: $c-gray;
			&:hover {
				opacity: 0.7;
			}
		}

		li + li {
			margin-left: 8px;
			padding-left: 15px;

			&:before {
				content: '';
				position: absolute;
				left: 0;
				top: 0;
				bottom: 0;
				width: 8px;
				height: 10px;
				margin: auto;
				background-image: url(../images/svg/ico-arrow-1.svg);
				background-size: 100% 100%;
				background-repeat: no-repeat;
			}
		}
	}
}

@media #{$large-desktop} {
	.breadcrumbs-alt {
		padding: 17px 40px;
	}
}

@media #{$small-desktop} {
	.breadcrumbs-alt {
		padding: 17px 20px;
	}
}

@media #{$tablet-portrait} {
	.breadcrumbs-alt {
		padding: 17px 0;
		.shell {
			padding: 0 20px;
		}
	}
}
