/* ------------------------------------------------------------ *\
	Dropdown
\* ------------------------------------------------------------ */

.dropdown {
	background-color: $c-white;
	border: 1px solid $c-light-gray-2;
	box-sizing: border-box;
	box-shadow: 0px 10px 12px rgba(0, 0, 0, 0.06);
	border-radius: 4px;
	min-width: 138px;
	padding: 8px;
	ul {
		list-style-type: none;
		li + li {
			margin-top: 4px;
		}
		a {
			display: block;
			font-size: 14px;
			padding: 10px 8px;
			border-radius: 4px;

			&:hover {
				background: rgba($c-red, 0.1);
				color: $c-red;
			}
		}
	}

	.dropdown__title {
		display: block;
		font-weight: 500;
		margin-bottom: 10px;
	}

	.dropdown__group + .dropdown__group {
		margin-top: 18px;
	}

	.dropdown__actions {
		margin-top: 16px;
	}
}

.dropdown--small {
	min-width: 56px;
}

.dropdown--alt {
	min-width: 280px;
	padding: 16px;
}

.dropdown-search {
	min-width: 415px;
	background-color: $c-light-gray-2;
	border: 1px solid $c-light-gray-2;
	// border-radius: 4px;
	box-shadow: 0px 10px 12px rgba($c-black, 0.06);

	.dropdown__head {
		padding: 11px 16px;
		p {
			display: inline-block;
			vertical-align: middle;
			margin-left: 4px;
			text-transform: uppercase;
			color: $c-light-gray-8;
			font-weight: 500;
			font-size: 12px;
		}
	}
 
	.dropdown__content {
		padding: 0;
	}

	.dropdown__group {
		background-color: $c-white;
	}

	.dropdown__group + .dropdown__group {
		margin-top: 1px;
	}
}

@media #{$tablet-portrait} {
	.dropdown-search {
		min-width: 100%;
	}
}

@media #{$mobile} {
	.dropdown-search {
		height: 100%;
	}
}
