/* ------------------------------------------------------------ *\
	Popup
\* ------------------------------------------------------------ */

.body-blackout {
	position: absolute;
	z-index: 1010;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: rgba($c-white, 0.8);
	display: none;

	&.is-blacked-out {
		display: block;
	}
}

@media #{$tablet-portrait} {
	.body-blackout {
		bottom: 0;
		right: 0;
		height: 100vh;
	}
}

.popup {
	display: inline-block;
	min-width: 450px;
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	pointer-events: none;
	opacity: 0;
	min-height: 400px;
	z-index: 1060;
	background-color: $c-white;
	box-shadow: 10px 10px 10px rgba($c-black, 0.25);
	border: 1px solid $c-light-gray-2;
	transition: .2s;

	&.is--visible {
		opacity: 1;
		pointer-events: auto;
	}

	.popup__head {
		h4 {
			font-weight: 500;
		}

		p {
			font-weight: 400;
			margin-top: 8px;
			color: $c-gray;
		}
	}

	.popup__close {
		position: absolute;
		top: 13px;
		right: 13px;
		width: 25px;
		height: 25px;
		z-index: 5;
		padding: 5px;

		svg {
			display: block;
			width: 100%;
			height: 100%;
		}

		@media #{$mobile} {
			top: 16px;
			right: 14px;
		}
	}

	.popup__head {
		margin-bottom: 16px;
	}

	.popup__title {
		font-weight: 400;
		text-transform: uppercase;
		color: $c-light-gray-8;
		margin-bottom: 7px;
	}

	.popup__group + .popup__group {
		margin-top: 12px;
		padding-top: 13px;
		border-top: 1px solid $c-light-gray-2;
	}

	.popup__body {
		margin-bottom: 60px;
	}

	.popup__actions {
		position: fixed;
		left: 0;
		bottom: 0;
		right: 0;
		width: 100%;
		padding: 6px 12px;
		background-color: $c-white;
		border-top: 1px solid $c-light-gray-2;
		transform: translate3d(0,0,0);

		.btn {
			width: 100%;
		}
	}

	.popup__inner {
		padding: 40px;
	}

	@media #{$tablet-portrait} {
		min-width: 100%;
		height: 100vh;
		left: 0;
		top: 0;
		transform: translate(0);

		.popup__inner {
			position: absolute;
			top: 0;
			bottom: 0;
			width: 100%;
			overflow-y: scroll;
			-webkit-overflow-scrolling: touch;
		}
	}
}

.popup--search {
	min-width: 570px;

	.popup__head {
		margin-bottom: 28px;
	}

	.popup__group + .popup__group {
		margin-top: 34px;
		padding-top: 0;
		border: none;
	}
	@media #{$tablet-portrait} {
		min-width: 100%;
	}
}

.popup-access {
	display: inline-block;
	min-width: 900px;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	pointer-events: none;
	opacity: 0;
	max-height: 560px;
	min-height: 560px;
	height: 100%;
	z-index: 5060;
	background-color: $c-white;
	box-shadow: 10px 10px 10px rgba($c-black, 0.25);
	border: 1px solid $c-light-gray-7;

	&.is--visible {
		opacity: 1;
		pointer-events: auto;
	}

	.popup__cols {
		height: 100%;
		display: flex;
	}

	.popup__col {
		height: 100%;
		&--1of2 {
			width: 50%;
		}
	}

	.popup__inner {
		padding: 52px;
		height: 100%;
	}

	.popup__image {
		height: 100%;
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
	}

	.popup__close {
		position: absolute;
		top: 12px;
		right: 12px;
		background-color: $c-black;
		width: 32px;
		height: 32px;
		border-radius: 50%;
		text-align: center;
		line-height: 32px;
	}
}

@media #{$small-desktop} {
	.popup-access {
		min-width: 90%;

		.popup__inner {
			padding: 20px;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-wrap: wrap;
		}
	}
}

@media #{$mobile} {
	.popup-access {
		min-width: 100%;
		max-height: 100vh;
		min-height: -webkit-fill-available;
		height: 100vh;

		.popup__head {
			min-height: 52px;
			border-bottom: 1px solid $c-light-gray-2;
		}

		.popup__inner {
			padding: 24px 12px;
			align-items: flex-start;
		}

		.popup__col {
			&--1of2 {
				width: 100%;
			}
		}

		.popup__close {
			right: 8px;
			top: 8px;
			background-color: transparent;
		}
	}
}

/* ------------------------------------------------------------ *\
	Popup Product
\* ------------------------------------------popup__close------------------ */

.popup--product {
	min-height: auto;
	max-width: 600px;
	width: 100%;
	padding: 0;
	border-radius: 0;
	box-shadow: 2px 0px 2px rgba($c-black, 0.10),
		0px 2px 2px rgba($c-black, 0.10);

	.popup__close {
		top: 16px;
		right: 17px;
	}
	
	.popup__inner {
		position: relative;
		padding: 0px;
	}
	
	.popup__body {
		margin: 0px;
	}
}

@media #{$tablet-portrait} {
	.popup--product {
		position: fixed;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		min-width: 450px;
		min-height: auto;
		height: auto;
	}
}

@media #{$mobile} {
	.popup--product {
		bottom: 0;
		top: auto;
		min-width: 100%;
		left: 0;
		transform: translate(0);
		min-height: auto;

		.popup__body {
			height: 100%;
		}
	}
}

