/* ------------------------------------------------------------ *\
	Popup Property
\* ------------------------------------------------------------ */

.popup-property {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	opacity: 0;
	pointer-events: none;
	z-index: 1060;
	background-color: $c-white;
	&.is--visible {
		opacity: 1;
		pointer-events: auto;
	}
}

/* ------------------------------------------------------------ *\
	Popup Share
\* ------------------------------------------------------------ */

.popup--share {
	max-width: 450px;
	min-height: auto;

	.popup__inner {
		padding: 40px;
	}

	.popup__group {
		p {
			line-height: 1.36;
		}
	}
}

@media #{$mobile} {
	.popup--share {

		.popup__inner {
			padding: 67px 0 30px;
		}

		.popup__head {
			padding: 0 32px;
		}

		.popup__groups {
			padding: 0 12px;
		}

		.popup__group {
			& > p {
				padding: 0 20px;
			}

			.link {
				padding: 0 20px;
			}

			.socials {
				margin-top: -13px;
			}
		}
	}
}

/* ------------------------------------------------------------ *\
	Popup Filter
\* ------------------------------------------------------------ */

.popup--filter {
	padding: 0 0 52px;
	height: calc(var(--vh, 1vh) * 100);

	.popup__head {
		position: relative;
		width: 100%;
		background-color: $c-white;
		text-align: center;
		margin-bottom: 0;
		padding: 15px 0;
		border-bottom: 1px solid $c-light-gray-2;
		h4 {
			font-size: 18px;
			font-weight: 500;
		}
	}

	.popup__close {
		top: 13px;
		right: 13px;
	}

	.popup__inner {
		padding: 0px;
	}
	
}
