/* ------------------------------------------------------------ *\
	Price
\* ------------------------------------------------------------ */

.prices {
	display: flex;
	justify-content: space-between;
}
.price {
	width: 48%;
	.price__list {
		opacity: 0;
		visibility: hidden;
		transition: opacity 0.3s, visibility 0.3s;
		li {
			span {
				display: block;
				font-size: 14px;
				padding: 10px 8px;
				border-radius: 4px;
				cursor: pointer;
				&:hover {
					background: rgba($c-red, 0.1);
					color: $c-red;
				}
			}
		}
		&.active {
			opacity: 1;
			visibility: visible;
		}
	}

	.price__field {
		margin-bottom: 10px;
		font-size: 16px;
		padding: 11px;
		border: 1px solid $c-light-gray-3;
		border-radius: 4px;
		max-width: 104px;
		width: 100%;
		color: $c-dark-gray;

		@media #{$tablet-portrait} {
			max-width: 100%;
		}

		&:focus {
			border-color: $c-dark-gray;
		}
	}
}

/* ------------------------------------------------------------ *\
	Price Small
\* ------------------------------------------------------------ */

.price-small {
	font-size: 28px;
	font-weight: 600;
	display: inline-block;
	vertical-align: middle;
}

@media #{$mobile} {
	.price-small {
		font-size: 24px;
	}
}

/* ------------------------------------------------------------ *\
	Price Large
\* ------------------------------------------------------------ */

.price-large {
	font-size: 1.75rem;
    line-height: 1.2;
	font-weight: 550;
	display: block;
	margin-top: .5rem;
}
