/* ------------------------------------------------------------ *\
	Base
\* ------------------------------------------------------------ */

body {
	min-width: 320px;
	background: #fff;
	font-family: $font-family-sans-serif;
	font-size: $font-size-base;
	line-height: $line-height-base;
	color: $c-gray;
	
	&.active {
		position: fixed;
		height: 100vh;
  		overflow-y: hidden;
	}

	&.freeze {
		height: 100vh;
		position: fixed;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		overflow-y: scroll;
	}

	.backdrop {
		display: none;
		z-index: 1010;
		background: #000;
		opacity: .5;
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		overflow: hidden;
		-webkit-animation-name: f;
		animation-name: f;
		-webkit-animation-duration: .3s;
		animation-duration: .3s;
		-webkit-animation-timing-function: ease;
		animation-timing-function: ease;
		-webkit-animation-fill-mode: forwards;
		animation-fill-mode: forwards;
		will-change: transform;
	}
	
	.is-backdrop-on {
		display: block;
	}
}
.c-gray {
	background-color: $c-light-gray-10;
}

.c-red {
	color: $c-red !important;
}

.c-light-green {
	color: $c-light-green !important;
}

@media #{$mobile} {
	.c-white-mobile {
		background-color: $c-white;
	}
}

a {
	color: inherit;
	text-decoration: none;
	transition: opacity 0.3s;

	&:hover,
	&[href^='tel'] {
		text-decoration: none;
	}
}

h1 {
	font-size: 32px;
	@media #{$mobile} {
		font-size: 24px;
	}
}

h2 {
	font-size: 28px;
}

h4 {
	font-size: 24px;

	// @media #{$mobile} {
	// 	font-size: 18px;
	// }
}

h5 {
	font-size: 20px;
	@media #{$mobile} {
		font-size: 16px;
	}
}

h6 {
	font-size: 14px;
}
