/* ------------------------------------------------------------ *\
	Paging
\* ------------------------------------------------------------ */

.paging {
	text-align: center;
	margin: auto 10px;

	.paging__actions {
		display: flex;
		justify-content: center;
		align-items: center;
	}

	.paging__next,
	.paging__prev {
		background-color: $c-light-gray-4;
		border-radius: 4px;
		padding: 18px 21px;
		text-decoration: none;
		position: relative;
		min-width: 48px;
		height: 48px;
		transition: background 0.3s;
		svg {
			stroke: $c-base;
		}
		&:hover {
			background-color: $c-red;
			svg {
				stroke: $c-white;
			}
		}
	}

	.ico-arrow-next,
	.ico-arrow-prev {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		margin: auto;
	}

	.paging__actions {
		ul {
			margin: 0 10px;
			list-style-type: none;
			li {
				display: inline-block;
				vertical-align: middle;
			}

			li + li {
				margin-left: 5px;
			}

			a {
				display: block;
				font-size: 18px;
				font-weight: 500;
				text-decoration: none;
				height: 48px;
				padding: 0 15px;
				min-width: 48px;
				line-height: 48px;
				background-color: $c-light-gray-4;
				border-radius: 4px;
				transition: background 0.3s, color 0.3s;
			}

			li {
				&:hover {
					a {
						background-color: $c-red;
						color: $c-white;
					}
				}
			}

			.current {
				a {
					background-color: $c-red;
					color: $c-white;
				}
			}
		}
	}

	.paging__spacer {
		font-size: 18px;
		font-weight: 500;
		text-decoration: none;
		height: 48px;
		width: 48px;
		line-height: 48px;
		background-color: $c-light-gray-4;
		border-radius: 4px;
		transition: background 0.3s, color 0.3s;
		&:hover {
			background-color: $c-red;
			color: $c-white;
		}
	}

	.paging__info {
		margin-bottom: 24px;
	}

	.paging__breadcrumbs {
		margin-top: 38px;
		ul {
			li {
				display: inline-block;
				position: relative;
				font-size: 14px;
			}

			a {
				text-decoration: none;
			}

			li + li {
				padding-left: 20px;
				margin-left: 10px;

				&:before {
					content: '';
					position: absolute;
					top: 0;
					bottom: 0;
					left: 0;
					margin: auto;
					width: 10px;
					height: 7px;
					background-image: url(../images/svg/ico-arrow-1.svg);
					background-size: 100% 100%;
				}
			}
		}
	}
}

@media #{$mobile} {
	.paging {
		.paging__info {
			margin-bottom: 12px;
		}

		.paging__actions {
			justify-content: center;
		}

		.paging__prev,
		.paging__next {
			margin: 5px;
			min-width: 15%;
			font-size: 16px;
			font-weight: 500;
			padding: 12px 21px;
			span {
				display: inline-block;
				vertical-align: middle;
			}
		}

		.ico-arrow-next {
			margin-left: 6px;
		}
		.ico-arrow-prev {
			margin-right: 6px;
		}
		.ico-arrow-next,
		.ico-arrow-prev {
			display: inline-block;
			vertical-align: middle;
			position: static;
			svg {
				display: block;
			}
		}
	}
}
