/* ------------------------------------------------------------ *\
	Header
\* ------------------------------------------------------------ */

.header {
	box-shadow: inset 0px -1px 0px $c-light-gray-2;
	top: 0;
	left: 0;
	right: 0;
	z-index: 20;
	width: 100%;
	background-color: $c-white;

	.header__inner {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 12px 40px;
	}

	.header__aside,
	.header__head {
		display: flex;
		align-items: center;
	}

	.header__outer {
		box-shadow: inset 0px 1px 0px $c-light-gray-2;
	}

	.btn-access {
		margin-left: 20px;
	}

	.filters-trigger {
		padding: 10px 0 10px 20px;
	}
}

@media #{$small-desktop} {
	.header {
		.header__inner {
			padding: 12px 10px;
		}
	}
}

@media #{$tablet-portrait} {
	.header {
		.header__inner {
			padding: 8px 20px;
		}

		.header__head {
			width: 100%;
		}

		.header__aside {
			position: fixed;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			width: 80%;
			max-width: 300px;
			height: 100vh;
			background-color: $c-white;
			align-items: flex-start;
			flex-direction: column;
			overflow-y: scroll;
			opacity: 0;
			transform: translateX(-100%);
			-webkit-transform: translateX(-100%);
			transition: all .3s ease;
			z-index: 5000;
    		will-change: transform;

			&.open {
				opacity: 1;
				-webkit-transform: translateX(0);
				transform: translateX(0);
			}
		}

		.header__aside-head {
			width: 100%;
			order: 1;
			display: flex;
			justify-content: space-between;
			padding: 8px 12px;
			align-items: center;
			box-shadow: inset 0px -1px 0px $c-light-gray-2;
			.nav-trigger--active {
				margin-right: 0;
			}
		}

		.btn-access {
			order: 2;
			display: block;
			width: 94%;
			margin: 14px auto 20px;
		}

		.nav {
			order: 3;
			width: 100%;
		}
	}
}

@media #{$mobile} {
	.header {
		z-index: 51;
		.header__aside {
			padding-bottom: 50px;
			padding-left: 20px;
			padding-right: 20px;
		}

		.header__aside-head {
			min-height: 52px;
		}

		.header__head {
			width: 100%;
		}
		
		.btn-access {
			min-height: 46px;
		}
	}
}
