/* ------------------------------------------------------------ *\
	Fullsize Image
\* ------------------------------------------------------------ */

.fullsize-image {
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center;
	padding-bottom: 100%;
	.lazyloaded {
		opacity: 0;
		width: 1px;
		height: 1px;
	}
}
