/* ------------------------------------------------------------ *\
	Switch
\* ------------------------------------------------------------ */

.switch {
	.switch__title {
		font-size: 14px;
		display: inline-block;
		vertical-align: middle;
		margin-right: 4px;
		h5 {
			font-weight: 500;
		}
	}

	.switch__holder {
		position: relative;
		width: 51px;
		height: 28px;
		display: inline-block;
		vertical-align: middle;
	}

	input {
		position: absolute;
		top: 0;
		left: 0;
		z-index: 2;
		opacity: 0;
		cursor: pointer;
		width: 51px;
		height: 28px;

		&:checked {
			z-index: 1;
		}
	}
	.switch__toggle-outside {
		position: absolute;
		width: 100%;
		left: 0;
		height: 100%;
		padding: 2px;
		overflow: hidden;
		border-radius: 100px;
		background-color: $c-dark-gray;
		transition: 0.25s ease all;
	}

	.switch__toggle-inside {
		position: absolute;
		left: 2px;
		border-radius: 50%;
		width: 24px;
		height: 24px;
		background-color: $c-white;
		transition: 0.25s ease all;
		&:after,
		&:before {
			content: '';
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			margin: auto;
			background-size: 100% 100%;
			transition: opacity 0.3s, visibility 0.3s;
		}

		&:before {
			background-image: url(../images/svg/ico-close.svg);
			width: 9px;
			height: 9px;
		}

		&:after {
			background-image: url(../images/svg/ico-check.svg);
			width: 11px;
			height: 7px;
			opacity: 0;
			visibility: hidden;
		}
	}

	input ~ input:checked ~ .switch__toggle-outside {
		background-color: $c-red;
	}

	input ~ input:checked ~ .switch__toggle-outside .switch__toggle-inside {
		left: 25px;
		&:before {
			opacity: 0;
			visibility: hidden;
		}
		&:after {
			opacity: 1;
			visibility: visible;
		}
	}
}
