/* ------------------------------------------------------------ *\
	Tag
\* ------------------------------------------------------------ */

.tag {
	// text-transform: uppercase;
	font-size: 14px;
	font-weight: 500;
	padding: 5px 10px;
	border-radius: 4px;
	color: $c-white;
	background-color: $c-green;
	display: inline-block;
	vertical-align: middle;

	&--blue {
		background-color: $c-blue;
	}

	&--red {
		background-color: $c-red;
	}
}

@media #{$mobile} {
	.tag {
		font-size: 10px;
	}
}
