/* ------------------------------------------------------------ *\
	Widgets
\* ------------------------------------------------------------ */

.widgets {
	list-style: none outside none;

	li + li {
		margin-top: 25px;
	}
}

/* ------------------------------------------------------------ *\
	Widget Info
\* ------------------------------------------------------------ */

.widget-info {
	border: 1px solid $c-light-gray-2;
	border-radius: 4px;
	padding: 24px;

	.widget__head {
		margin-bottom: 15px;
		h5 {
			font-size: 18px;
			font-weight: 500;
		}
	}

	.widget__content {
		ul {
			list-style-type: none;
		}

		li {
			position: relative;
			padding-left: 17px;

			&:before {
				content: '';
				position: absolute;
				left: 0;
				top: 5px;
				width: 5px;
				height: 5px;
				border-radius: 50%;
				background-color: $c-light-gray-8;
			}
		}

		a {
			text-decoration: none;
			color: $c-red;
		}

		li + li {
			margin-top: 16px;
		}
	}
}
