.svg-sprite {
	#ico-share,
	#ico-map,
	#ico-search,
	#ico-lock-stroke,
	#ico-arrow-1,
	#ico-arrow-3 {
		path {
			stroke: inherit;
		}
	}

	#ico-share,
	#ico-map,
	#ico-lock-stroke {
		stroke: inherit;
	}

	#ico-heart-outlined {
		fill: inherit;
		path {
			stroke: inherit;
		}
	}
	#ico-camera-2,
	#ico-pin-filled,
	#ico-globe-filled,
	#ico-walking {
		fill: inherit;
		path {
			fill: inherit;
		}
	}

	#ico-heart {
		path {
			fill: inherit;
		}
	}

	#ico-plus-circle {
		fill: inherit;
		path {
			fill: inherit;
		}
	}

	#ico-city,
	#ico-airplane,
	#ico-train-filled,
	#ico-bus,
	#ico-trolley,
	#ico-user-2 {
		fill: inherit;
		path {
			fill: inherit;
		}
	}
}
