/* ------------------------------------------------------------ *\
	Accordion
\* ------------------------------------------------------------ */

.accordion {
	.accordion__head {
		position: relative;
		display: flex;
		align-items: center;
		background-color: $c-light-gray-13;
		padding: 10px 16px;
		border-radius: 4px;

		h6 {
			margin-left: 19px;
			font-weight: 500;
		}
		.ico-arrow-2 {
			position: absolute;
			top: 18px;
			right: 19px;
			width: 15px;
			height: 9px;
			transition: transform 0.3s;
		}
	}

	/* .accordion__body {
		overflow: hidden;
		max-height: 0;
		transition: max-height 350ms ease-in-out;
	} */

	.accordion__inner {
		padding: 20px 56px;
		ul {
			list-style-type: none;

			span {
				color: $c-light-gray-8;
			}

			li + li {
				margin-top: 20px;
			}
		}
	}

	.accordion__section {
		&.open {
			.accordion__head {
				.ico-arrow-2 {
					transform: rotate(-180deg);
				}
			}
		}
	}

	.accordion__section + .accordion__section {
		margin-top: 10px;
	}
}

@media #{$tablet-portrait} {
	.js-accordion {
		position: relative;
		/* overflow: hidden; */

		/* .js-accordion-expand {
			opacity: 0;
			overflow: hidden;
