/* ------------------------------------------------------------ *\
	Filter
\* ------------------------------------------------------------ */

.filter {
	padding: 16px 40px;
	display: flex;
	justify-content: space-between;

	.filter__inner {
		display: flex;
		align-items: center;
	}

	.filter__holder {
		& > ul {
			display: flex;
		}
	}

	.filter__select {
		position: relative;
		display: block;
		padding: 7px 11px;
		border: 1px solid $c-light-gray-3;
		border-radius: 4px;
		font-size: 14px;
		text-decoration: none;
		margin-right: 8px;
		cursor: pointer;
		transition: background-color 0.3s, font-weight 0.3s;

		&:hover,
		&.active {
			background-color: $c-light-gray-6;
		}

		&.active {
			font-weight: 500;
		}

		a {
			text-decoration: none;
		}

		@media #{$small-desktop} {
			margin-right: 4px;
		}
	}

	.filter__dropdown {
		position: absolute;
		top: 100%;
		left: 0;
		margin-top: 5px;
		opacity: 0;
		visibility: hidden;
		transition: opacity 0.3s, visibility 0.3s;
		z-index: 10;
		&.active {
			opacity: 1;
			visibility: visible;
		}
	}

	.filter__reset {
		font-size: 14px;
		color: $c-red;
		font-weight: 400;
		text-decoration: none;
		margin-left: 26px;

		span {
			display: inline-block;
			vertical-align: middle;
			margin-left: 3px;
		}

		.ico-reset {
			display: inline-block;
			vertical-align: middle;
			width: 14px;
			height: 15px;
		}

		&:hover {
			opacity: 0.8;
		}

		@media #{$small-desktop} {
			margin-left: 20px;
		}
	}

	.filter__aside {
		display: flex;
		align-items: center;
	}

	.btn {
		margin-left: 3px;
	}

	.btn-sort {
		margin-right: 35px;
		@media #{$small-desktop} {
			margin-right: 20px;
		}
	}
}

@media #{$small-desktop} {
	.filter {
		padding: 16px 10px;
	}
}

/* ------------------------------------------------------------ *\
	Filter Mobile
\* ------------------------------------------------------------ */

.filter-mobile {
	height: 100%;
	overflow-y: auto;
	.filter__inner {
		padding: 0 12px;
	}

	.filter__actions {
		position: fixed;
		left: 0;
		bottom: 0;
		right: 0;
		width: 100%;
		padding: 6px 12px;
		border-top: 1px solid $c-light-gray-2;

		.btn {
			width: 100%;
		}
	}

	.filter__title {
		display: block;
		font-weight: 500;
		margin-bottom: 12px;
	}

	.filter__group {
		padding: 15px 0 16px;
	}

	.filter__reset {
		display: block;
		margin: 10px auto 91px;
		max-width: 145px;
		font-size: 14px;
		color: $c-red;
		font-weight: 400;
		text-decoration: none;
		border: 1px solid $c-red;
		text-align: center;
		border-radius: 100px;
		padding: 8px 16px;

		span {
			display: inline-block;
			vertical-align: middle;
			margin-left: 3px;
		}

		.ico-arrow-reset {
			display: inline-block;
			vertical-align: middle;
			width: 24px;
			height: 24px;
		}

		&:hover {
			opacity: 0.8;
		}
	}

	.textarea {
		width: 100%;
	}

	.filter__group + .filter__group {
		border-top: 0px solid $c-light-gray-2;
	}
}
