/* ------------------------------------------------------------ *\
	Places
\* ------------------------------------------------------------ */

.places {
	.places__add {
		text-decoration: none;
		display: block;
		margin-top: 16px;

		p {
			display: inline-block;
			vertical-align: middle;
			color: $c-red;
			margin-left: 16px;
			font-weight: 500;
		}

		&:hover {
			.places__plus {
				background: rgba($c-red, 0.4);
			}
		}
	}

	.places__plus {
		display: inline-block;
		vertical-align: middle;
		width: 44px;
		height: 44px;
		background: rgba($c-red, 0.12);
		border-radius: 50%;
		text-align: center;
		line-height: 44px;
		transition: background 0.3s;
	}

	.places__info {
		margin: 16px 0;
	}
}

/* ------------------------------------------------------------ *\
	Place
\* ------------------------------------------------------------ */

.place {
	display: flex;
	justify-content: space-between;
	align-items: center;
	position: relative;
	padding: 18px 0 18px 60px;
	border-bottom: 1px solid $c-light-gray-2;

	.place__icon {
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		margin: auto;
		width: 44px;
		height: 44px;
		line-height: 41px;
		border: 1px solid $c-light-gray-3;
		border-radius: 50%;
		text-align: center;

		i {
			svg {
				fill: $c-base;
			}
		}

		&--active {
			background-color: $c-dark-green;
			border-color: $c-dark-green;

			i {
				svg {
					fill: $c-white;
				}
			}
		}

		&--green {
			background-color: $c-light-green;
			border-color: $c-light-green;
		}

		&--orange {
			background-color: $c-orange;
			border-color: $c-orange;
		}

		&--rating {
			span {
				color: $c-white;
				font-size: 16px;
				font-weight: 500;
			}
		}
	}

	.place__content {
		p {
			font-weight: 500;
			margin-bottom: 5px;
		}
	}

	.place__aside {
		span {
			font-weight: 500;
			margin-right: 31px;
		}

		a + a {
			margin-left: 12px;
		}
	}
}

/* Place School */

.place--school {
	padding: 12px 0 12px 60px;
	.place__content {
		p {
			margin-bottom: 0;
		}
		& > span {
			font-size: 12px;
			color: $c-light-gray-8;
		}
	}
}

@media #{$mobile} {
	.place {
		flex-wrap: wrap;

		.place__content {
			width: 100%;
		}

		.place__aside {
			margin-top: 10px;
			width: 100%;
		}
	}
}
