/* ------------------------------------------------------------ *\
	Socials
\* ------------------------------------------------------------ */

.socials {
	ul {
		list-style-type: none;
		display: flex;
	}

	li {
		flex-grow: 1;
		text-align: center;
	}

	a {
		text-align: center;
		display: block;
		text-decoration: none;
	}

	i {
		display: block;
		margin: 0 auto 18px;
	}
}

@media #{$mobile} {
	.socials {
		ul {
			flex-direction: column;
		}

		li {
			padding: 14px 20px;
			text-align: left;
			border-bottom: 1px solid $c-light-gray-2;
		}

		a {
			text-align: left;
		}

		i {
			display: inline-block;
			vertical-align: middle;
			margin: 0;
			margin-right: 12px;
		}
	}
}
