/* ------------------------------------------------------------ *\
	Items
\* ------------------------------------------------------------ */

.item {
	padding: 18px 24px 14px 32px;
	border: 1px solid $c-light-gray-10;
	border-radius: 4px;
	position: relative;
	display: flex;
	align-items: center;
	justify-content: space-between;
	background-color: $c-white;
	transition: background 0.3s;
	& > a {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}

	.item__head {
		display: flex;
		align-items: center;
		margin-bottom: 10px;
		h6 {
			margin-left: 13px;
			font-size: 16px;
			font-weight: 500;
		}
	}

	.item__content {
		p {
			color: $c-gray;
		}
	}

	&:hover {
		background-color: $c-light-gray-5;
	}
}

.item + .item {
	margin-top: 12px;
}

@media #{$tablet-portrait} {
	.item {
		padding: 18px;
	}
}

@media #{$mobile} {
	.item {
		padding: 14px;

		.item__inner {
			width: 80%;
		}
	}
}
