/* ------------------------------------------------------------ *\
	Search
\* ------------------------------------------------------------ */

.search {
	//box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.08);
	border-radius: 4px;
	border: 1px solid $c-light-gray-2;
	padding: 10px 16px;
	min-width: 320px;
	position: relative;
	margin-left: 20px;
	margin-right: 20px;
	form {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.search__field {
		border: none;
		color: $c-black;
		width: 100%;
		padding-top: 2px;
		&::placeholder {
			color: $c-light-gray-1;
		}
	}

	.search__btn {
		border: none;
		background-color: transparent;
		padding-right: 10px;
		.ico-search {
			width: 16px;
			height: 16px;
			svg {
				stroke: #777;
			}
		}
	}

	.search__dropdown {
		position: absolute;
		top: 40px;
		left: 0;
		margin-top: 11px;
		z-index: 55;
		overflow-y: auto;
		max-height: 500px;
		opacity: 0;
		visibility: hidden;
		transition: opacity 0.3s, visibility 0.3s;

		&.active {
			opacity: 1;
			visibility: visible;
		}
	}
}

@media #{$tablet-portrait} {
	.search {
		width: 100%;
		margin-left: 0px;
		margin-right: 0px;
		.search__dropdown {
			width: 100%;
		}
	}
}

@media #{$mobile} {
	.search {
		position: static;
		max-width: 100%;
		min-width: auto;

		.search__dropdown {
			top: 55px;
			margin-top: 0;
			height: 100vh;
			max-height: 100vh;
		}

		/* &.active {
			width: 87%;
			min-width: 87%;
		} */
	}
}

/* ------------------------------------------------------------ *\
	Search Items
\* ------------------------------------------------------------ */

.search-items {
	width: 100%;
	max-width: 770px;
	margin: auto;
	padding: 56px 0;

	.search__head {
		text-align: center;
		margin-bottom: 24px;
		h2 {
			font-weight: 500;
		}

		h6 {
			font-size: 16px;
			font-weight: 500;
			margin-top: 20px;
		}
	}

	.search__actions {
		text-align: center;
		margin-top: 12px;
	}
}

.search-items--empty {
	text-align: center;
	.search__head {
		margin-bottom: 9px;
	}

	.search__actions {
		margin-top: 25px;
	}
}

@media #{$tablet-portrait} {
	.search-items {
		padding: 20px 0;

		.search__head {
			h2 {
				font-size: 20px;
			}
		}
	}
}

@media #{$mobile} {
	.search-items--empty {
		min-height: 344px;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
	}
}
