/* ------------------------------------------------------------ *\
	Panel
\* ------------------------------------------------------------ */

.panel {
	padding-left: 20px;

	.panel__head {
		margin-bottom: 20px;
		display: flex;
		justify-content: space-between;
	}

	.panel__title {
		position: relative;
		h2 {
			margin-bottom: 3px;
			font-weight: 500;
		}

		p {
			color: $c-light-gray-8;
		}

		&:before {
			content: '';
			position: absolute;
			left: -20px;
			top: 0;
			bottom: 0;
			width: 3px;
			height: 100%;
			background-color: $c-red;
		}

		.tag {
			margin-top: 10px;
		}
	}

	.panel__subtitle {
		font-size: 0.90rem;
	}

	.panel__aside {
		text-align: right;
		.price-small {
			margin-left: 10px;
		}

		.statistics {
			margin-top: 10px;
		}
	}

	.panel__groups {
		&--no-border {
			border-bottom: none;
		}
	}

	.panel__group {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		align-items: center;
		margin-bottom: 34px;
		padding-bottom: 25px;
		border-bottom: 1px solid $c-light-gray-2;

		&--no-border {
			border-bottom: none;
			padding-bottom: 0;
			margin-bottom: 30px;
		}
	}

	.panel__content {
		p {
			line-height: 1.71;
		}

		h2 {
			font-weight: 500;
		}

		h4 {
			font-weight: 500;
		}

		p + h6 {
			margin-top: 27px;
		}

		& > h6 {
			margin-bottom: 6px;
		}

		& > h5 {
			font-size: 18px;
			font-weight: 500;
			margin-bottom: 15px;
		}

		&--top-border {
			margin-top: 20px;
		}

		ul + h5 {
			margin-top: 34px;
		}

		& > .btn-border {
			margin: 20px 0 27px;
		}

		p + p {
			margin-top: 27px;
		}

		.statistics--large {
			margin-top: 10px;
			margin-bottom: 25px;
		}
	}

	.panel__table {
		width: 100%;
	}

	.panel__cols {
		display: flex;
	}

	.panel__col {
		&--1of2 {
			width: 50%;
		}
	}

	.panel__content {
		width: 100%;
	}

	.panel__map {
		margin-bottom: 20px;
		width: 100%;
		padding-bottom: 33%;
		position: relative;
		img {
			position: absolute;
			top: 0;
			left: 0;
		}
	}

	.panel__subtitle {
		
		&--for-sale, &--for-rent {
			font-size: 20px;
			font-weight: 600;
		}
	}

	.panel__actions {
		margin-top: 17px;
		p {
			margin-bottom: 11px;
		}
		strong {
			font-weight: 500;
		}
	}

	.panel__dated {
		color: $c-light-gray-8;
	}

	.products {
		width: 100%;
	}
}

@media #{$small-desktop} {
	.panel {
		padding-left: 0;

		.panel__title {
			&:before {
				display: none;
			}
		}
	}
}

@media #{$tablet-portrait} {
	.panel {
		.panel__head {
			padding-bottom: 0;
			margin-bottom: 0;
			flex-direction: column;

			.list-rooms {
				margin: 10px 0 15px 0;
			}

			&--border {
				border-bottom: 1px solid $c-light-gray-2;
			}

			&--accordion {
				h2 {
					margin-bottom: 0;
				}
			}
		}

		.panel__map {
			img {
				width: 100%;
			}
		}

		.panel__title {
			padding: 25px 0 23px;
			position: relative;
			border-bottom: none;

			h2 {
				font-size: 1.5rem;
			}

			p {
				padding-right: 2rem;
			}

			.ico-arrow-2 {
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				margin: auto;
				width: 15px;
				height: 9px;
				transition: transform 0.3s;
			}
		}

		.panel__group {
			flex-direction: column;
			align-items: flex-start;
			padding-bottom: 0;
			margin-bottom: 19px;
			border: none;
			margin-bottom: 0;
			& > h2 {
				font-size: 1.375rem!important;
				line-height: 1.3!important;
				font-weight: 500;
				margin-bottom: 25px;
			}

			& > p {
				margin-bottom: 10px;
			}
		}

		.panel__groups {
			&--accordion {
				/* display: none; */
				// overflow: hidden;
				// max-height: 0;
				// transition: max-height 350ms ease-in-out;
				border-bottom: 1px solid $c-light-gray-2;

				.panel__group {
					// padding-top: 16px;

					&:last-child {
						padding-bottom: 35px;
					}
				}
			}

			&--no-border {
				border: none;
			}
		}

		.panel__content {
			p + p {
				margin-top: 16px;
			}
		}

		.list-actions-alt {
			margin-bottom: 20px;
			margin-top: 20px;
		}
	}

	.panel--first {
		.panel__head {
			padding-bottom: 12px;
			margin-bottom: 10px;
		}

		.panel__title {
			padding: 0;
		}
	}

	.panel--mobile-small {
		.panel__head {
			padding: 0;
		}

		.panel__title {
			padding: 0 0 12px;
			border-bottom: none;

			h2 {
				margin: 0;
				padding: 12px 0;
			}
		}
	}

	.panel--foot {
		.panel__title {
			.ico-arrow-2 {
				right: 4px;
			}
		}
		.panel__groups {
			&--accordion {
				border: none;
			}
		}
	}
}

@media #{$mobile} {
	.panel {
		.panel__title {
			h2 {
				margin-bottom: 8px;
			}
		}

		.panel__head {
			&--accordion {
				.panel__title {
					padding: 25px 0 25px;
				}
			}
		}

		.panel__price {
			p {
				padding: 5px 0 7px;
			}

			.statistics {
				padding: 0;
			}
		}

		.panel__content {
			p {
				font-size: 1rem;
    			line-height: 1.5;
			}
		}

		.panel__cols {
			flex-wrap: wrap;
		}

		.panel__col {
			width: 100%;
		}

		.panel__group + .panel__group {
			margin-top: 22px;
		}

		.list-actions-alt {
			margin-bottom: 0px;
			margin-top: 0px;
		}
	}
	.panel--first {
		.panel__title {
			border-bottom: none;
			h2 {
				font-size: 24px;
			}
		}

		.panel__content {
			p {
				line-height: 1.5;
			}

			p + p {
				margin-top: 26px;
			}
		}
	}
}
