/* ------------------------------------------------------------ *\
	Bar
\* ------------------------------------------------------------ */

.bar {
	// box-shadow: 0px -1px 0px $c-light-gray-2;
	box-shadow: 0 5px 5px -3px $c-light-gray-2;
	border: 1px solid $c-light-gray-2;
	position: fixed;
	bottom: 0;
	left: 0;
	right: 0;
	width: 100%;
	background-color: $c-white;
	z-index: 50;
	padding: 5px 10px;
	font-size: .875rem!important;
	color: rgba(0,0,0,.54)!important;
	font-weight: 600!important;

	ul {
		display: flex;
		list-style-type: none;
		align-items: center;
		justify-content: space-between;
		li {
			text-align: center;
		}

		span {
			margin-top: 5px;
			display: block;
		}

		li + li {
			border-left: 0px solid $c-light-gray-2;
		}
	}

	.ico-sort-down-solid {
		width: 20px;
		height: 20px;
	}

	.ico-map-marker-solid {
		width: 20px;
		height: 20px;
	}

	.ico-bars-solid {
		width: 20px;
		height: 20px;
	}

	.ico-pin-2 {
		width: 20px;
		height: 20px;
	}

	.ico-th-list-solid {
		width: 20px;
		height: 20px;
	}

	.ico-list {
		width: 20px;
		height: 20px;
	}

	.ico-globe-solid {
		width: 20px;
		height: 20px;
	}

	.ico-bell-solid {
		width: 20px;
		height: 20px;
	}

	.ico-bell-solid-active {
		width: 20px;
		height: 20px;
	}

	.ico-save-search {
		width: 20px;
		height: 20px;
	}

	.ico-search-solid {
		width: 20px;
		height: 20px;
	}

	.ico-globe-filled {
		svg {
			fill: $c-gray;
		}
	}

	.nav-trigger {
		margin-right: 0;
    	margin-left: 0;
	}

	.btn {
		padding: 5px;
	}
}

.bar--alt {
	padding: 6px 12px;

	.btn {
		padding: 12px 17px;
	}
	
	a {
		display: block;
	}
	ul {
		li {
			flex-grow: 0;
			width: 48%;
		}
		li + li {
			border-left: none;
			margin-left: 11px;
		}
	}
}
