/* ------------------------------------------------------------ *\
	Sidebar
\* ------------------------------------------------------------ */

.sidebar {
	top: 0;
	bottom: 0;
	right: 0;
	width: 49%;
	height: calc(100vh - 132px);
	background-size: cover;
	background-repeat: no-repeat;
	background-position: bottom;
	overflow: hidden;
	transition: width 0.3s;
	z-index: 5;

	&.fixed {
		top: 132px;
		bottom: 0;
		position: fixed;
	}

	&.absolute {
		top: auto;
		bottom: 0;
	}

	&.active {
		width: 100%;
	}

	.sidebar__inner {
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		z-index: 5;

		.list-actions {
			height: 100%;
			.map-buttons-tr {
				position: absolute;
				top: 1.5rem;
				right: 1.5rem;
			}
			.map-buttons-br {
				position: absolute;
				bottom: 1.5rem;
				right: 1.5rem;
			}
			.map-buttons-tl {
				position: absolute;
				top: 1.5rem;
				left: 1.5rem;
			}
		}
	}
}

@media #{$tablet-portrait} {
	.sidebar {
		width: 100%;
		height: 100%;
		bottom: 0;
		top: 0;
		
		.sidebar__inner {
			.list-actions {

				.map-buttons-tr {
					top: 1rem;
					right: 1rem;
				}
				.map-buttons-br {
					bottom: 1rem;
					right: 1rem;
				}
				.map-buttons-tl {
					top: 1rem;
					left: 1rem;
				}
			}
		}
		&.absolute {
			height: calc(100vh - 118px);
			width: 100%;
			bottom: 60px;
		}
	}
}
