/* ------------------------------------------------------------ *\
	Tabs
\* ------------------------------------------------------------ */

.tabs {
	.tabs__nav {
		margin-bottom: 35px;

		li {
			display: inline-block;
		}

		a {
			font-size: 32px;
			color: $c-light-gray-8;
			font-weight: 500;
			text-decoration: none;
			padding-bottom: 16px;
			display: block;
			border-bottom: 2px solid transparent;
			transition: border-color 0.3s, color 0.3s;

			&.is-active {
				color: $c-red;
				border-color: $c-red;
			}

			&:hover {
				border-color: $c-red;
				color: $c-red;
			}

			@media #{$tablet-portrait} {
				font-size: 24px;
			}

			@media #{$mobile} {
				padding-bottom: 12px;
			}
		}

		li + li {
			margin-left: 39px;

			@media #{$mobile} {
				margin-left: 22px;
			}
		}

		@media #{$mobile} {
			margin-bottom: 27px;
		}
	}

	.tabs__body {
		max-width: 435px;
	}
}

/* Tabs Alt */

.tabs--alt {
	.shell {
		max-width: 100%;
	}
	.tabs__body {
		max-width: 100%;
	}

	.tabs__head {
		border-bottom: 1px solid $c-light-gray-2;
	}

	.tabs__nav {
		margin-bottom: 0;
		padding: 0 30px;
		a {
			padding: 22px 0 15px;
			font-size: 16px;
		}

		li + li {
			margin-left: 27px;
		}
	}
}

/* @media #{$large-desktop} {
	.tabs--alt {
		.tabs__nav {
			padding: 0;
		}
	}
} */

@media #{$small-desktop} {
	.tabs--alt {
		.tabs__nav {
			padding: 0;
		}
	}
}

@media #{$tablet-portrait} {
	.tabs--alt {
		.tabs__nav {
			ul {
				overflow-y: hidden;
				overflow-x: auto;
				-webkit-overflow-scrolling: touch;
				white-space: nowrap;
			}
			a {
				padding: 15px 0 6px;
			}

			li + li {
				margin-left: 19px;
			}
		}
	}
}

/* Tabs Product */

.tabs--product {
	.tabs__head {
		padding: 9px 0;
		border-bottom: 1px solid $c-light-gray-2;
		.shell {
			display: flex;
			align-items: center;
			justify-content: space-between;
		}
	}

	.tabs__nav {
		margin-bottom: 0;
		a {
			padding-bottom: 0;
			font-size: 14px;
			color: $c-gray;
			text-align: center;
			border: none;
			i {
				margin-bottom: 5px;
				pointer-events: none;
			}
			.ico-camera-2,
			.ico-pin-filled,
			.ico-globe-filled,
			.ico-walking {
				svg {
					fill: $c-gray;
				}
			}

			span {
				display: block;
				pointer-events: none;
			}

			&.is-active {
				border: none;

				.ico-camera-2,
				.ico-pin-filled,
				.ico-globe-filled,
				.ico-walking {
					svg {
						fill: $c-red;
					}
				}
			}

			&:hover {
				border: none;
				.ico-camera-2,
				.ico-pin-filled,
				.ico-globe-filled,
				.ico-walking {
					svg {
						fill: $c-red;
					}
				}
			}
		}
	}

	.tabs__aside {
		display: flex;
		align-items: center;
		.ico-arrow-back {
			margin-right: 21px;
			width: 20px;
			height: 20px;
		}
	}

	.tabs__body {
		width: 100%;
		max-width: 100%;
	}
}

@media #{$mobile} {
	.tabs--product {
		.tabs__nav {
			a {
				span {
					font-size: 12px;
				}
			}
		}
	}
}

/* Tabs Places */

.tabs--places {
	.tabs__head {
		margin-bottom: 20px;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.tabs__nav {
		margin-bottom: 0;
		a {
			font-size: 16px;
			font-weight: 700;
			border-bottom-width: 3px;
			padding-bottom: 0;
			&.ia-active {
				color: $c-base;
			}
		}

		li + li {
			margin-left: 15px;
		}
	}

	.tabs__body {
		max-width: 100%;
	}
}

/* ------------------------------------------------------------ *\
	Tabs
\* ------------------------------------------------------------ */

.tab {
	opacity: 0;
	visibility: hidden;
	height: 0;
	transition: opacity 0.3s, visibility 0.3s, height 0.3s;

	&.is-visible {
		opacity: 1;
		visibility: visible;
		height: auto;
	}

	.list-checks {
		margin-bottom: 37px;
	}

	@media #{$mobile} {
		.list-checks {
			margin-bottom: 27px;
		}
	}
}

/* Tab Alt */

.tab--alt {
	max-width: 100%;
	padding: 0 30px;

	@media #{$small-desktop} {
		padding: 0;
	}
}

/* Tab Product */

.tab--product {
	.tab__body {
		height: calc(100vh - 64px);
		overflow-y: scroll;
		-webkit-overflow-scrolling: touch;
	}

	.tab__image {
		padding-bottom: 0;
		height: 100%;
	}
}
