/* ------------------------------------------------------------ *\
	Pins
\* ------------------------------------------------------------ */

.pin {
	width: 20px;
	height: 20px;
	background-color: $c-red;
	border-radius: 50%;
	display: block;
	border: 2px solid $c-white;
	box-shadow: 0px 2px 7px rgba($c-red, 1);
	position: relative;
	text-decoration: none;
	text-align: center;

	&--plus {
		&:before {
			content: '+';
			position: absolute;
			left: 4px;
			top: 0px;
			font-size: 14px;
			font-weight: 500;
			color: $c-white;
			line-height: 17px;
		}
	}
}
