/* ------------------------------------------------------------ *\
	Link
\* ------------------------------------------------------------ */

.link {
	color: $c-red;
	text-decoration: none;
	span {
		display: inline-block;
		vertical-align: middle;
		margin-left: 4px;
		font-size: 12px;
	}

	&--alt {
		span {
			font-size: 14px;
			color: $c-base;
			margin-left: 8px;
		}

		&:hover {
			span {
				color: $c-red;
			}
		}
	}
}

/* Link Filter */

.link--filter {
	.ico-arrow-reset {
		width: 16px;
		height: 16px;
	}
}
